import {
  SafeAreaView,
  StyleSheet,
  Text,
  useWindowDimensions,
} from 'react-native';
import Header from '../../component/AppBar';
import {
  Card,
  ExpandableSection,
  Image,
  TouchableOpacity,
  View,
} from 'react-native-ui-lib';
import InstallmentSchedule from './InstallmentSchedule';
import {gestureHandlerRootHOC} from 'react-native-gesture-handler';
import {
  image_dropdown,
  image_rightArrow12px,
} from '../../../assets/images/images';
import {useCallback, useState} from 'react';
import {SceneMap, TabBar, TabView} from 'react-native-tab-view';
import {useFocusEffect} from '@react-navigation/native';
import {getDebtBriefTimeLine} from '../../api/DebitApi';
import {handleShowDate} from '../../utlis/func';
import {getContractElmsById} from '../../api/ElmsApi';
import i18n from '../../service/i18n';
import ContractInfo from '../../component/ContractInfo';

const RegisServiceContract = (props: any) => {
  console.log(props,'props')
  const [expand, setExpand] = useState(true);
  const [index, setIndex] = useState(0);
  const layout = useWindowDimensions();
  const [dataUserLoanBrief, setDataUserLoanBrief] = useState<any>();
  const [dataTimeLine, setDataTimeLine] = useState<any>();
  const [routes] = useState([
    {key: 'first', title: i18n.t('loan.registerContract.profileInfo')},
    {key: 'second', title: i18n.t('loan.registerContract.detailed')},
  ]);

  useFocusEffect(
    useCallback(() => {
      (async () => {
        const dataUserLoanBriefs = await getContractElmsById(
          props.route.params.data.briefId,
        );
        setDataUserLoanBrief(dataUserLoanBriefs);
        const dataTimeLines = await getDebtBriefTimeLine(
          props.route.params.data.briefId,
        );
        setDataTimeLine(dataTimeLines);
      })();
    }, []),
  );

  const renderTabBar = (props: any) => {
    return (
      <TabBar
        {...props}
        indicatorStyle={{backgroundColor: '#62248F'}}
        style={{backgroundColor: '#FFF'}}
        labelStyle={{
          color: '#ccc',
          fontSize: 10,
          fontWeight: '400',
          fontFamily: 'Inter',
        }}
        activeColor="#62248F"
      />
    );
  };

  const ProfileInformation = () => {
    let createdDate;
    if (dataUserLoanBrief) {
      createdDate = handleShowDate(dataUserLoanBrief.createdDate);
    }
    return (
      <View
        style={{
          flex: 1,
          flexDirection: 'column',
          width: '100%',
          height: '100%',
        }}>
        <View
          style={{
            flex: 3,
            backgroundColor: '#FFF',
            width: '100%',
            height: '100%',
            marginTop: '2%',
            paddingHorizontal: 17.5,
          }}>
          <ExpandableSection
            top={false}
            expanded={expand}
            sectionHeader={
              <View
                style={{
                  flexDirection: 'row',
                  alignItems: 'center',
                  justifyContent: 'space-between',
                  marginTop: 20,
                }}>
                <Text style={styles.textTitle}>
                  {i18n.t('loan.registerContract.generalInformation')}
                </Text>
                <TouchableOpacity onPress={() => setExpand(!expand)}>
                  {expand ? (
                    <Image
                      style={{width: 18, height: 12}}
                      source={image_dropdown}
                    />
                  ) : (
                    <Image
                      style={{width: 18, height: 12}}
                      source={image_rightArrow12px}
                    />
                  )}
                </TouchableOpacity>
              </View>
            }>
            <Card key={0} style={{marginBottom: 10}}>
              <View
                style={{
                  flexDirection: 'column',
                  alignItems: 'flex-start',
                  justifyContent: 'center',
                }}>
                <View style={styles.rowValue}>
                  <Card.Section
                    content={[
                      {
                        text: `${i18n.t(
                          'loan.registerContract.profileName',
                        )} :`,
                        text12: true,
                        style: {
                          justifyContent: 'flex-start',
                          fontWeight: '600',
                          lineHeight: 20,
                          color: '#00204D',
                        },
                      },
                    ]}
                    style={{paddingLeft: 10, flex: 1}}
                  />
                  <Card.Section
                    content={[
                      {
                        text: dataUserLoanBrief
                          ? dataUserLoanBrief.briefName
                          : '',
                        text12: true,
                        style: {
                          fontWeight: '400',
                          lineHeight: 16,
                          color: '#00204D',
                        },
                      },
                    ]}
                    style={{paddingLeft: 10, flex: 2}}
                  />
                </View>
                <View style={styles.rowValue}>
                  <Card.Section
                    content={[
                      {
                        text: `${i18n.t(
                          'loan.registerContract.profileCode',
                        )} :`,
                        text12: true,
                        style: {
                          fontWeight: '600',
                          lineHeight: 20,
                          color: '#00204D',
                        },
                      },
                    ]}
                    style={{paddingLeft: 10, flex: 1}}
                  />
                  <Card.Section
                    content={[
                      {
                        text: dataUserLoanBrief
                          ? dataUserLoanBrief.briefCode
                          : '',
                        text12: true,
                        style: {
                          fontWeight: '400',
                          lineHeight: 16,
                          color: '#00204D',
                        },
                      },
                    ]}
                    style={{paddingLeft: 10, flex: 2}}
                  />
                </View>
                <View
                  style={{
                    flexDirection: 'row',
                    columnGap: 50,
                    marginTop: 10,
                    marginBottom: 10,
                    alignItems: 'flex-start',
                  }}>
                  <Card.Section
                    content={[
                      {
                        text: `${i18n.t('loan.registerContract.creator')} :`,
                        text12: true,
                        style: {
                          fontWeight: '600',
                          lineHeight: 20,
                          color: '#00204D',
                        },
                      },
                    ]}
                    style={{paddingLeft: 10, flex: 1}}
                  />
                  <View style={{flexDirection: 'row', flex: 2}}>
                    <Card.Section
                      imageSource={
                        dataUserLoanBrief
                          ? dataUserLoanBrief.avatarCustomer
                          : null
                      }
                      imageStyle={{height: 30, width: 30}}
                    />

                    <View>
                      <Card.Section
                        content={[
                          {
                            text: dataUserLoanBrief
                              ? dataUserLoanBrief.name
                              : '',
                            text12: true,
                            style: {
                              fontWeight: '400',
                              lineHeight: 16,
                              color: '#00204D',
                            },
                          },
                        ]}
                        style={{paddingLeft: 10}}
                      />
                      <Card.Section
                        content={[
                          {
                            text: dataUserLoanBrief
                              ? dataUserLoanBrief.email
                              : '',
                            text12: true,
                            style: {
                              fontWeight: '400',
                              lineHeight: 16,
                              color: '#00204D',
                            },
                          },
                        ]}
                        style={{paddingLeft: 10}}
                      />
                    </View>
                  </View>
                </View>
                <View style={styles.rowValue}>
                  <Card.Section
                    content={[
                      {
                        text: `${i18n.t('home.bodyHome.agency')} :`,
                        text12: true,
                        style: {
                          fontWeight: '600',
                          lineHeight: 20,
                          color: '#00204D',
                        },
                      },
                    ]}
                    style={{paddingLeft: 10, flex: 1, justifyContent: 'center'}}
                  />
                  <Card.Section
                    content={[
                      {
                        text: dataUserLoanBrief ? dataUserLoanBrief.agency : '',
                        text12: true,
                        style: {
                          fontWeight: '400',
                          lineHeight: 16,
                          color: '#00204D',
                        },
                      },
                    ]}
                    style={{paddingLeft: 10, flex: 2}}
                  />
                </View>
                <View style={styles.rowValue}>
                  <Card.Section
                    content={[
                      {
                        text: `${i18n.t(
                          'loan.registerContract.dateCreated',
                        )} :`,
                        text12: true,
                        style: {
                          fontWeight: '600',
                          lineHeight: 20,
                          color: '#00204D',
                        },
                      },
                    ]}
                    style={{paddingLeft: 10, flex: 1}}
                  />
                  <Card.Section
                    content={[
                      {
                        text: dataUserLoanBrief ? createdDate : '',
                        text12: true,
                        style: {
                          fontWeight: '400',
                          lineHeight: 16,
                          color: '#00204D',
                        },
                      },
                    ]}
                    style={{paddingLeft: 10, flex: 2}}
                  />
                </View>
              </View>
            </Card>
          </ExpandableSection>
          <ContractInfo dataBriefId={props.route.params.data.briefId} />
        </View>
      </View>
    );
  };

  const renderScene = SceneMap({
    first: ProfileInformation,
    second: () => InstallmentSchedule(dataTimeLine),
  });

  return (
    <SafeAreaView
      style={{
        backgroundColor: '#fff',
        flex: 1,
      }}>
      <Header
        backgroundColor="#FFF"
        label={i18n.t('loan.registerContract.serviceRegistration')}
        {...props}
      />
      <TabView
        lazy={({route}) => route.key === 'second'}
        renderTabBar={renderTabBar}
        navigationState={{index, routes}}
        renderScene={renderScene}
        onIndexChange={setIndex}
        initialLayout={{width: layout.width}}
      />
    </SafeAreaView>
  );
};

const styles = StyleSheet.create({
  generalInfo: {
    flex: 1,
    paddingHorizontal: 8,
  },
  rowValue: {
    flexDirection: 'row',
    columnGap: 50,
    marginTop: 10,
    marginBottom: 10,
    alignItems: 'center',
  },
  textTitle: {
    fontWeight: '600',
    fontSize: 16,
    lineHeight: 24,
    color: '#00204D',
  },
  // textLabel: {
  //   fontWeight: '600',
  //   fontSize: 12,
  //   color: '#00204D',
  //   width: 145,
  //   height: 20,
  //   lineHeight: 20,
  // },
  // textValue: {
  //   fontWeight: '400',
  //   fontSize: 12,
  //   width: 192,
  //   height: 16,
  //   lineHeight: 16,
  //   color: '#00204D',
  // },
});

export default gestureHandlerRootHOC(RegisServiceContract);
