import { Dimensions, Platform, PixelRatio } from 'react-native';
const { width } = Dimensions.get('window');

export const handleShowString = (name: string, length = 30, numberSub = 20) => {
  if (!name) return ""
  if (name.length > length) {
    return name.substring(0, numberSub) + " ..."
  } else {
    return name
  }
}

export const handleShowDate = (date: string, showDateWithSecond = false, showOnlyDate = false) => {
  if (!date) return ""
  const newDate = new Date(date);
  newDate.setHours(newDate.getHours() - 7);
  const day = newDate.getDate() > 10 ? newDate.getDate() : "0" + newDate.getDate();
  const month = newDate.getMonth() + 1 > 10 ? newDate.getMonth() + 1 : "0" + (newDate.getMonth() + 1);
  const year = newDate.getFullYear();

  const min = newDate.getMinutes() > 10 ? newDate.getMinutes() : "0" + newDate.getMinutes();
  const hour = newDate.getHours() > 10 ? newDate.getHours() : "0" + newDate.getHours();
  const second = newDate.getSeconds() > 10 ? newDate.getSeconds() : "0" + newDate.getSeconds();

  if (showOnlyDate) {
    return `${day}/${month}/${year}`
  }
  if (!showDateWithSecond) {
    return `${day}/${month}/${year} ${hour}:${min}`
  } else {
    return `${day}/${month}/${year} • ${hour}:${min}:${second}`
  }
}

export const roundNumber = (data: any, number = 3) => {
  const amount = Math.round(Number(data)).toFixed(0).replace(/(\d)(?=(\d{3})+\b)/g, "$1.");
  return amount + "đ";
}



export const handleMoney = (money: number, float = true) => {
  if (!money) return 0;
  let newNumber = money;
  if(float){
    return newNumber.toFixed(3).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
  }else{
    return newNumber.toFixed(0).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")
  }


}



const guidelineBaseWidth = 390;
// const guidelineBaseHeight = 680;

const scale = function (size: number) {
  if (Platform.OS === 'ios') {
    if (Platform.isPad) {
      return Math.round((width / guidelineBaseWidth) * size);
    }
    return Math.round(
      PixelRatio.roundToNearestPixel((width / guidelineBaseWidth) * size),
    );
  } else {
    return Math.round(PixelRatio.roundToNearestPixel(size));
  }
};
export { scale };
