import React, { memo, useState } from "react";
import { Box, HamburgerIcon, Menu, Pressable } from 'native-base';
import { Image, Text, TouchableOpacity } from "react-native-ui-lib";
import { image_Icon_clear, image_file_copies, image_more_horiz } from "../../assets/images/images";
import { StyleSheet } from "react-native";
import ModalComponent from "./ModalComponent";
import i18n from "../service/i18n"

const DialogComponent = () => {
  const [openModal, setOpenModal] = useState(false);
  return (
    <>
      <Box w="80%" alignItems="center">
        <Menu right={10} top={2} borderRadius={20} w="150" trigger={triggerProps => {
          return <Pressable accessibilityLabel="More options menu" {...triggerProps}>
            <Image source={image_more_horiz} width={20} height={20} />
          </Pressable>;
        }}>
          <Menu.Item style={styles.diver}>
            <Image width={16} height={16} source={image_file_copies} />
            <Text style={styles.text}>{i18n.t("contractManager.cloned")}</Text>
          </Menu.Item>
          <Menu.Item onPress={() => {
            setOpenModal(true);
          }}>
            <Image width={16} height={16} source={image_Icon_clear} />
            <Text style={styles.text}>{i18n.t("contractManager.cancelProfile")}</Text>
          </Menu.Item>
        </Menu>
      </Box>
      {openModal && (
        <ModalComponent open={openModal} setOpen={setOpenModal} />
      )}

    </>
  )
}

const styles = StyleSheet.create({
  text: {
    color: "rgba(0, 32, 77, 0.95)",
    fontSize: 14,
    fontWeight: "400"
  },
  diver: {
    borderBottomColor: 'rgba(0, 53, 128, 0.15)',
    borderTopColor: "transparent",
    borderLeftColor: "transparent",
    borderRightColor: "transparent",
    borderWidth: 0.5,
    borderStyle: "solid"
  }
})

export default DialogComponent;