import React, { memo, useCallback, useState } from "react";
import { Dimensions, SafeAreaView, StyleSheet } from "react-native";
import { ActionSheet, Card, Chip, Dialog, Image, Incubator, PanningProvider, Text, TouchableOpacity, View, WheelPicker } from "react-native-ui-lib";
import { image_iVay, image_kita, image_leftArrow, image_more_horiz } from "../../../assets/images/images";
import { colors } from "../../../assets/color/color";
import { GestureHandlerRootView } from "react-native-gesture-handler";
import ModalComponent from "../../component/ModalComponent";
import DialogComponent from "../../component/DialogComponent";
import { handleShowDate, handleShowString, scale } from "../../utlis/func";
import { LoanStatus } from "../../utlis/enum";
// import Dialog from "react-native-dialog";
import i18n from "../../service/i18n"

const { width, height } = Dimensions.get("screen")

const ContractItemComponent = (props: any) => {
  const handleShowStatus = (status: LoanStatus) => {
    switch (status) {
      case LoanStatus.ChoXacNhan: return i18n.t("chechStatusMyContract.waitComfirm")
      case LoanStatus.ChoDuyet: return i18n.t("chechStatusMyContract.pending")
      case LoanStatus.DangDuyet: return i18n.t("chechStatusMyContract.browsing")
      case LoanStatus.DaDuyet: return i18n.t("chechStatusMyContract.approved")
      case LoanStatus.TuChoiDuyet: return i18n.t("chechStatusMyContract.refuseApprove")
      case LoanStatus.ChoThamDinh: return i18n.t("chechStatusMyContract.waitEvaluation")
      case LoanStatus.DangThamDinh: return i18n.t("chechStatusMyContract.underEvaluation")
      case LoanStatus.DaThamDinh: return i18n.t("chechStatusMyContract.appraised")
      case LoanStatus.TuChoiThamDinh: return i18n.t("chechStatusMyContract.refuseAppraisal")
      case LoanStatus.DangKiKet: return i18n.t("chechStatusMyContract.signing")
      case LoanStatus.DaKiKet: return i18n.t("chechStatusMyContract.signed")
      case LoanStatus.ChuaGiaiNgan: return i18n.t("chechStatusMyContract.notYetDisbursed")
      case LoanStatus.DangGiaiNgan: return i18n.t("chechStatusMyContract.disbursement")
      case LoanStatus.DaGiaiNgan: return i18n.t("chechStatusMyContract.disbursed")
      default: return ""
    }
  }

  const getBackgroundStyle = (status: LoanStatus) => {
    switch (status) {
      case LoanStatus.ChoXacNhan: return ''
      case LoanStatus.ChoDuyet: return ''
      case LoanStatus.DangDuyet: return 'rgba(239, 93, 168, 0.1)'
      case LoanStatus.DaDuyet: return 'rgba(34, 197, 94, 0.1)'
      case LoanStatus.TuChoiDuyet: return 'rgba(236, 47, 47, 0.1)'
      case LoanStatus.ChoThamDinh: return ''
      case LoanStatus.DangThamDinh: return ''
      case LoanStatus.DaThamDinh: return ''
      case LoanStatus.TuChoiThamDinh: return 'rgba(236, 47, 47, 0.1)'
      case LoanStatus.DangKiKet: return ''
      case LoanStatus.DaKiKet: return ''
      case LoanStatus.ChuaGiaiNgan: return ''
      case LoanStatus.DangGiaiNgan: return ''
      case LoanStatus.DaGiaiNgan: return ''
      default: return "rgba(0, 163, 255, 0.10)"
    }
  }

  const getLableStyle = (status: LoanStatus) => {
    switch (status) {
      case LoanStatus.ChoXacNhan: return ''
      case LoanStatus.ChoDuyet: return ''
      case LoanStatus.DangDuyet: return 'rgba(239, 93, 168, 1)'
      case LoanStatus.DaDuyet: return 'rgba(34, 197, 94, 1)'
      case LoanStatus.TuChoiDuyet: return 'rgba(236, 47, 47, 1)'
      case LoanStatus.ChoThamDinh: return ''
      case LoanStatus.DangThamDinh: return ''
      case LoanStatus.DaThamDinh: return ''
      case LoanStatus.TuChoiThamDinh: return 'rgba(236, 47, 47, 1)'
      case LoanStatus.DangKiKet: return ''
      case LoanStatus.DaKiKet: return ''
      case LoanStatus.ChuaGiaiNgan: return ''
      case LoanStatus.DangGiaiNgan: return ''
      case LoanStatus.DaGiaiNgan: return ''
      default: return "rgba(0, 163, 255, 0.10)"
    }
  }

  const { navigation, item } = props;
  return (
    <>
      <SafeAreaView key={item.id}>
        <Card
          containerStyle={{ minHeight: 200 }}
          flex
          activeOpacity={1}
          marginR-30
          marginL-30
          marginT-10
          onPress={() => { navigation.navigate('ContractDetail', { id: item.id, briefName: item.briefName }) }}
          style={{ backgroundColor: "#fff" }}
        >
          <View>
            <View style={{ alignItems: "center", flexDirection: "row", justifyContent: "space-between", marginHorizontal: 20, paddingVertical: 10 }}>
              <View style={{ flexDirection: "row", justifyContent: "flex-start", columnGap: 12, alignItems: "center" }}>
                <View style={{ height: 40, alignItems: "center", justifyContent: "center" }}>
                  <Card.Section imageSource={{ uri: item.avatarCustomer }} imageStyle={{ height: 40, width: 40, borderRadius: 20 }} />
                </View>
                <View style={{ rowGap: 3 }}>
                  <Card.Section content={[{ text: handleShowString(item.fullName), text70: true, grey10: true, color: colors.colorText }]} />
                  <Card.Section content={[{ text: `${i18n.t("loan.registerContract.profileCode")} : ${handleShowString(item.briefCode, 10, 15)}`, text85: true, grey10: true, color: colors.text }]} />
                </View>
              </View>
              <View>
                <DialogComponent />
              </View>
            </View>

            <View style={{ borderStyle: "dashed", borderWidth: 1, borderColor: "#CBCBCB", marginHorizontal: 30 }}></View>
            <View style={{ paddingTop: 28, paddingRight: 13, paddingLeft: 16, paddingBottom: 13, rowGap: 15 }}>
              <View style={styles.containerText}>
                <Text style={styles.titleText}>{i18n.t("loan.registerContract.profileName")}</Text>
                <Text style={styles.subTitle}>{handleShowString(item.briefName)}</Text>
              </View>
              <View style={styles.containerText}>
                <Text style={styles.titleText}>{i18n.t("loan.registerContract.dateCreated")}</Text>
                <Text style={styles.subTitle}>{handleShowDate(item.creationDate)}</Text>
              </View>
              <View style={styles.containerText}>
                <Text style={styles.titleText}>{i18n.t("chechStatusMyContract.titleData.contractStatus")}</Text>
                <Text style={styles.subTitle}>
                  <Chip containerStyle={{ borderWidth: 0 }} backgroundColor={getBackgroundStyle(item.briefType)} labelStyle={{ color: getLableStyle(item.briefType), fontSize: scale(11) }} label={handleShowStatus(item.briefType)} onPress={() => console.log('pressed')} />
                </Text>
              </View>
            </View>
          </View>
        </Card>
      </SafeAreaView>

    </>
  )
}

const styles = StyleSheet.create({
  containerText: {
    alignItems: "center",
    justifyContent: "space-between",
    flexDirection: "row"
  },
  titleText: {
    fontSize: scale(15),
    fontWeight: "500",
    color: colors.colorTitie
  },
  subTitle: {
    fontSize: scale(12),
    fontWeight: "400",
    color: colors.colorTitie
  }
})

export default memo(ContractItemComponent);