import React, { useCallback, useEffect, useState } from "react";
import { FlatList, ImageBackground, SafeAreaView, ScrollView } from "react-native";
import { GridList, GridView, TouchableOpacity, View } from "react-native-ui-lib";
import InputComponent from "../../component/InputComponent";
import ContractItemComponent from "./ContractItem";
import PickerComponent from "../../component/PickerComponent";
import { Svg, Path } from "react-native-svg"
import Header from "../../component/AppBar";
import { chechStatusMyContract } from "../../utlis/data";
import { useFocusEffect } from "@react-navigation/native";
import { Center, Skeleton, Toast, VStack } from "native-base";
import AlertComponent, { AlertEnum } from "../../component/AlertComponent";
import { getLoanBriefs } from "../../api/DebitApi";
import i18n from "../../service/i18n"

const ContractComponent = (props: any) => {
  const [text, onChangeSearch] = useState('')
  const [listContract, setListContract] = useState<any>()
  const [statusContract, setStatusContract] = useState("all")
  const [listElmsContract, setListElmsContract] = useState<any>()
  const [statusProcessContract, setStatusProcessContract] = useState("all")
  const [statusShop, setStatusShop] = useState("all")
  const [statusProduct, setStatusProduct] = useState("all")
  const [statusDate, setStatusDate] = useState("all")
  const [page, setPage] = useState(0);
  const [refreshing, setRefreshing] = useState(false);

  useFocusEffect(useCallback(() => {
    const newList = chechStatusMyContract.map((item) => {
      switch (item.title) {
        case "chechStatusMyContract.titleData.contractStatus":
          return {
            ...item,
            value: statusContract,
            setValue: setStatusContract
          }
        case "home.bodyHome.agency":
          return {
            ...item,
            value: statusShop,
            setValue: setStatusShop
          }
        case "chechStatusMyContract.titleData.product":
          return {
            ...item,
            value: statusProduct,
            setValue: setStatusProduct
          }
        case "chechStatusMyContract.titleData.dateCreated":
          return {
            ...item,
            value: statusDate,
            setValue: setStatusDate
          }
        default: return;
      }
    })
    setListContract(newList);
  }, []))

  useFocusEffect(useCallback(() => {
    handleFetchData(page)
  }, [page]))

  useEffect(() => {
    if (refreshing) {
      handleFetchData(0)
    }
  }, [refreshing])

  useEffect(() => {
    console.log(statusContract, statusProcessContract, statusShop, statusProduct, statusDate);
  }, [statusContract, statusProcessContract, statusShop, statusProduct, statusDate])

  const renderItemContractItem = ({ item }: any) => {
    return (
      <ContractItemComponent item={item}  {...props} />
    )
  }

  const renderStatus = ({ item }: any) => {
    return (
      <View key={item.id} marginR-15 style={{ minWidth: 130 }}>
        <PickerComponent placeholder={i18n.t(item.title)} data={item.listDropDown} value={item.value} onChangeValue={item.setValue} />
      </View>
    )
  }

  const handleShowToast = () => {
    Toast.show({
      render: () => AlertComponent({ status: AlertEnum.success, title: "success" })
    })
  }

  const handleRefreshing = () => {
    setRefreshing(true);
    setPage(0)
  }

  const handleEndReach = () => {
    setPage(prev => ++prev)
  }

  const handleFetchData = async (page: number) => {
    const listContractElms = await getLoanBriefs(page);
    if (page == 0) {
      setListElmsContract(listContractElms)
      setRefreshing(false)
    } else {
      setListElmsContract([...listElmsContract, ...listContractElms])
    }
  }

  const renderSkelon = () => {
    return (
      <Center w="100%">
        <VStack w="85%" maxH={"250"} borderWidth="1" space={1} overflow="hidden" rounded="md" _dark={{
          borderColor: "coolGray.500"
        }} _light={{
          borderColor: "coolGray.200"
        }}>
          <Skeleton h="40" />
          <Skeleton.Text px="4" />
        </VStack>
      </Center>
    )
  }

  return (
    <View style={{ flex: 1, backgroundColor: "#F8FAFE", borderRadius: 17, position: "relative" }}>
      <Header containerStyle={{
        height: "15%",
      }} backgroundColor='#F2E0FE' color={'#62248F'} label={i18n.t("contractManager.myBrief")}{...props} />
      <SafeAreaView
        style={{
          backgroundColor: "rgba(255, 255, 255, 1)",
          position: "absolute",
          top: "13%",
          borderTopLeftRadius: 17,
          borderTopRightRadius: 17,
          height: "88%"
        }}>

        <TouchableOpacity onPress={handleShowToast} marginR-30 marginL-30 marginT-20 marginB-20>
          <InputComponent placeholder={i18n.t("home.search")} text={text} onChangeText={onChangeSearch} />
        </TouchableOpacity>
        <View>
          {listContract && (
            <FlatList
              data={listContract}
              renderItem={renderStatus}
              horizontal
              showsHorizontalScrollIndicator={false}
              numColumns={1}
              contentContainerStyle={{ paddingBottom: 20, paddingHorizontal: 30 }}
            />
          )}
        </View>
        {listElmsContract ? (
          <GridList
            data={listElmsContract}
            numColumns={1}
            showsHorizontalScrollIndicator={false}
            showsVerticalScrollIndicator={false}
            onEndReachedThreshold={0.5}
            onEndReached={handleEndReach}
            refreshing={refreshing}
            onRefresh={handleRefreshing}
            renderItem={renderItemContractItem}
          />
        ) : (
          <GridList
            data={[{}, {}]}
            numColumns={1}
            showsHorizontalScrollIndicator={false}
            showsVerticalScrollIndicator={false}
            renderItem={renderSkelon}
          />
        )}
      </SafeAreaView>
    </View>
  )
}

export default ContractComponent;