import { Alert, CloseIcon, HStack, IconButton, Stack, Text, VStack } from "native-base";
import { Dimensions } from "react-native";

export enum AlertEnum {
  success = "success",
  error = 'error',
  info = 'info',
  warning = 'warning'
}

interface IAlert {
  status: AlertEnum,
  title: string
}

export default function AlertComponent(info: IAlert) {

  const { width } = Dimensions.get("screen")
  return <Stack space={3} w="100%" minW={width - 50} maxW="400">
    <Alert variant={"top-accent"} w="100%" status={info.status}>
      <VStack space={2} flexShrink={1} w="100%">
        <HStack flexShrink={1} space={2} justifyContent="space-between">
          <HStack space={2} flexShrink={1}>
            <Alert.Icon mt="1" />
            <Text fontSize="md" color="coolGray.800">
              {info.title}
            </Text>
          </HStack>
          <IconButton variant="unstyled" _focus={{
            borderWidth: 0
          }} icon={<CloseIcon size="3" />} _icon={{
            color: "coolGray.600"
          }} />
        </HStack>
      </VStack>
    </Alert>
  </Stack>;
}