import {ActionSheet, Text, View} from 'react-native-ui-lib';
import {colors} from '../../../assets/color/color';
import {
  Dimensions,
  Image,
  SafeAreaView,
  StyleSheet,
  TouchableOpacity,
} from 'react-native';
import {image_close, image_rightArrow12px} from '../../../assets/images/images';
import {useState} from 'react';
import {handleShowDate, roundNumber} from '../../utlis/func';
import i18n from '../../service/i18n';

const InstallmentScheduleItem = (installmentObj: any) => {
  const [visible, setVisible] = useState(false);

  const datePayment = handleShowDate(
    installmentObj.itemInstallmentSchedule.debtBriefDate,
    false,
    true,
  );

  const renderTitleCustome = () => {
    return (
      <View
        key={installmentObj.itemInstallmentSchedule.id}
        style={{
          flexDirection: 'row',
          width: '100%',
          height: '20%',
          justifyContent: 'space-around',
          paddingLeft: '28%',
          alignItems: 'center',
          borderBottomWidth: 0.5,
          borderBottomColor: 'rgba(0, 53, 128, 0.15)',
          borderRadius: 12,
          borderColor: 'grey',
        }}>
        <Text
          style={{
            fontWeight: '600',
            lineHeight: 22,
            letterSpacing: 0,
            textAlign: 'center',
            fontSize: 14,
            color: colors.colorText,
          }}>
          {datePayment}
        </Text>
        <TouchableOpacity onPress={() => setVisible(false)}>
          <Image style={{height: 10, width: 10}} source={image_close} />
        </TouchableOpacity>
      </View>
    );
  };

  const renderActionView = () => {
    return (
      <View
        key={installmentObj.itemInstallmentSchedule.id}
        style={{
          marginHorizontal: 24,
          alignItems: 'center',
          justifyContent: 'center',
        }}>
        <View style={styles.rowAcs}>
          <Text style={styles.labelActionSheet}>
            {i18n.t('simulation.originalMoney')}
          </Text>
          <Text style={styles.labelActionSheet}>
            {roundNumber(installmentObj.itemInstallmentSchedule.totalPrincipal)}
          </Text>
        </View>
        <View style={styles.rowAcs}>
          <Text style={styles.labelActionSheet}>
            {i18n.t('simulation.interest')}
          </Text>
          <Text style={styles.labelActionSheet}>
            {roundNumber(installmentObj.itemInstallmentSchedule.totalProfit)}
          </Text>
        </View>
        <View style={styles.rowAcs}>
          <Text style={styles.labelActionSheet}>
            {i18n.t('simulation.principalInterest')}
          </Text>
          <Text style={styles.valueActionSheet}>
            {installmentObj.itemInstallmentSchedule.totalOrgItr
              ? roundNumber(installmentObj.itemInstallmentSchedule.totalOrgItr)
              : roundNumber(
                  installmentObj.itemInstallmentSchedule.totalPrincipal +
                    installmentObj.itemInstallmentSchedule.totalProfit,
                )}
          </Text>
        </View>
        <View style={styles.rowAcs}>
          <Text style={styles.labelActionSheet}>
            {i18n.t('simulation.remainingAmount')}
          </Text>
          <Text style={styles.valueActionSheet}>
            {roundNumber(installmentObj.itemInstallmentSchedule.remainingDebt)}
          </Text>
        </View>
      </View>
    );
  };

  const showModal = () => {
    setVisible(true);
    renderActionView();
  };

  const closeModal = () => {
    setVisible(false);
  };

  return (
    <SafeAreaView>
      <View
        style={{
          alignItems: 'center',
          flexDirection: 'row',
          justifyContent: 'space-between',
          paddingVertical: 24,
        }}>
        <View>
          <Text
            style={{
              fontSize: 14,
              fontWeight: '400',
              color: colors.colorText,
            }}>
            {datePayment}
          </Text>
        </View>
        <View
          style={{
            flexDirection: 'row',
            alignItems: 'center',
            justifyContent: 'center',
          }}>
          <Text>
            {roundNumber(
              installmentObj.itemInstallmentSchedule.totalPrincipal +
                installmentObj.itemInstallmentSchedule.totalProfit,
            )}
          </Text>
          <TouchableOpacity style={{}} onPress={() => showModal()}>
            <Image
              style={{marginLeft: 12, width: 6, height: 12}}
              source={image_rightArrow12px}
            />
          </TouchableOpacity>
        </View>
        <ActionSheet
          key={installmentObj.itemInstallmentSchedule.id}
          cancelButtonIndex={1}
          renderTitle={renderTitleCustome}
          visible={visible}
          containerStyle={{
            height: Dimensions.get('screen').height * 0.3,
          }}
          dialogStyle={{borderTopRightRadius: 12, borderTopLeftRadius: 12}}
          onDismiss={() => closeModal()}
          renderAction={() => renderActionView()}
          destructiveButtonIndex={0}
          options={[
            {
              label: `${installmentObj.itemInstallmentSchedule.id}`,
              onPress: () => closeModal(),
            },
          ]}
        />
      </View>
    </SafeAreaView>
  );
};

const styles = StyleSheet.create({
  labelActionSheet: {
    fontSize: 14,
    lineHeight: 22,
    fontWeight: '400',
    color: colors.colorText,
  },
  valueActionSheet: {
    fontSize: 14,
    lineHeight: 22,
    fontWeight: '500',
    color: colors.colorText,
  },
  rowAcs: {
    flexDirection: 'row',
    width: '100%',
    height: '20%',
    justifyContent: 'space-between',
  },
});

export default InstallmentScheduleItem;
