import {FlatList} from 'react-native';
import {Timeline} from 'react-native-ui-lib';
import {colors} from '../../../assets/color/color';
import InstallmentScheduleItem from './ActionSheet';
import React, {useCallback} from 'react';
import {StatusPayment} from '../../utlis/enum';
import {useFocusEffect} from '@react-navigation/native';

const InstallmentSchedule = (listInstallment: any) => {
  useFocusEffect(
    useCallback(() => {
      (async () => {})();
    }, []),
  );

  const RenderTimelineItem = (installmentObj: any) => {
    return (
      <Timeline
        bottomLine={
          installmentObj !== listInstallment[listInstallment.length - 1]
            ? {
                type: Timeline.lineTypes.SOLID,
                state: Timeline.states.CURRENT,
              }
            : undefined
        }
        point={
          installmentObj.type == StatusPayment.success
            ? {
                state: Timeline.states.SUCCESS,
                color: colors.primary,
                type: Timeline.pointTypes.OUTLINE,
              }
            : {
                color: 'gray',
                state: Timeline.states.NEXT,
              }
        }
        topLine={
          installmentObj !== listInstallment[0]
            ? {
                type: Timeline.lineTypes.SOLID,
                state: Timeline.states.CURRENT,
              }
            : undefined
        }>
        <InstallmentScheduleItem
          key={installmentObj.id}
          itemInstallmentSchedule={installmentObj}
        />
      </Timeline>
    );
  };

  return (
    <FlatList
      showsVerticalScrollIndicator={false}
      showsHorizontalScrollIndicator={false}
      data={listInstallment}
      renderItem={({item}) => RenderTimelineItem(item)}
    />
  );
};

export default InstallmentSchedule;
