import React from "react";
import { Platform, StyleSheet } from "react-native";
import { Image, TextField, View } from "react-native-ui-lib";
import { colors } from "../../assets/color/color";
import { image_search, image_dropdown } from "../../assets/images/images";
import { scale } from "../utlis/func";

interface ITextInput {
  placeholder?: string,
  style?: any,
  secureTextEntry?: boolean,
  onChangeText?: React.Dispatch<React.SetStateAction<string>>,
  text?: string
  floatingPlaceholder?: boolean
  showLeading?: boolean
  showTrailing?: boolean
  showTrailingDropdown?: boolean
  readOnly?: boolean
  editable?: boolean;
  propsOther?: any;
  showDropDown?: boolean;
}

const InputComponent = (props: ITextInput) => {
  const {
    placeholder,
    secureTextEntry = false,
    floatingPlaceholder = false,
    showTrailing = false,
    showTrailingDropdown = false,
    editable = true,
    showLeading = true,
    showDropDown = false,
    readOnly = false
  } = props;
  return (
    <TextField
      containerStyle={{
        ...props?.style,
        ...styles.textField,
        backgroundColor: !editable ? 'rgba(0, 32, 77, 0.1)' : "#fff",
      }}
      style={{
        fontSize: scale(14),
        fontWeight: "500",
        color: colors.colorTitie
      }}
      placeholder={placeholder}
      placeholderTextColor={colors.colorSubText}
      floatingPlaceholder={floatingPlaceholder}
      readOnly={readOnly}
      onChangeText={props.onChangeText}
      value={props.text}
      editable={editable}
      secureTextEntry={secureTextEntry}
      leadingAccessory={
        showLeading ? <Image style={{ width: 17, height: 17, marginRight: 20 }} source={image_search} /> : undefined
      }
      trailingAccessory={
        showTrailing ? <Image style={{ width: 17, height: 17, marginRight: 20 }} source={image_search} /> : showDropDown ? <Image style={{ width: 17, height: 17, marginRight: 0 }} source={image_dropdown} /> : undefined
      }
    />
  )
}

const styles = StyleSheet.create({
  textField: {
    borderRadius: scale(8),
    width: "100%",
    paddingHorizontal: 20,
    paddingVertical: Platform.OS === 'ios' ? 16 : 10,
    borderWidth: 1,
    borderStyle: "solid",
    borderColor: 'rgba(0, 53, 128, 0.15)',
    backgroundColor: "#fff",
    zIndex: 1000000
  }
})

export default InputComponent;