import { useFocusEffect } from "@react-navigation/native";
import { useCallback, useState } from "react";
import { GridList, Image, Text, Timeline, View } from "react-native-ui-lib"
import { getLoanHistoryById } from "../../api/DebitApi";
import { SafeAreaView, StyleSheet } from "react-native";
import { image_avatar } from "../../../assets/images/images";
import { handleShowDate, scale } from "../../utlis/func";
import { colors } from "../../../assets/color/color";

const ContractHistory = (props: any) => {
  const [dataHistory, setDataHistory] = useState<any>()
  useFocusEffect(useCallback(() => {
    (async () => {
      const data = await getLoanHistoryById(props.id);
      setDataHistory(data);
    })()
  }, []))

  const renderTimeline = ({ item, index }: any) => {
    return (
      <Timeline
        point={{
          icon: image_avatar,
          state: Timeline.states.SUCCESS,
          type: Timeline.pointTypes.OUTLINE
        }}

        topLine={index !== 0 ? {
          state: Timeline.states.CURRENT,
          type: Timeline.lineTypes.SOLID,
          width: 2,
          color: "rgba(0, 53, 128, 0.15)"

        } : undefined}
        bottomLine={index !== (dataHistory.length - 1) ? {
          state: Timeline.states.CURRENT,
          type: Timeline.lineTypes.SOLID,
          width: 2,
          color: "rgba(0, 53, 128, 0.15)"
        } : undefined}
      >
        {renderTimelineData(item)}
      </Timeline>
    )
  }

  const renderTimelineData = (item: any) => {
    return (
      <View style={{ rowGap: 6, height: 50 }}>
        <Text style={styles.title}>{item.detail}</Text>
        <View style={{ flexDirection: "row", columnGap: 10 }}>
          <Text style={styles.email}>{handleShowDate(item.actionTime, true)} bởi </Text>
          <Image source={image_avatar} width={16} height={16} />
          <Text style={styles.email}>{item.email}</Text>
        </View>
      </View>
    )
  }

  return (
    <SafeAreaView style={{ marginTop: 10, backgroundColor: "#fff", height: "100%" }}>
      {dataHistory && (
        <GridList
          numColumns={1}
          data={dataHistory}
          renderItem={renderTimeline}
          itemSpacing={0}
        />
      )}
    </SafeAreaView>
  )
}

const styles = StyleSheet.create({
  title: {
    fontSize: scale(14),
    fontWeight: "600",
    color: colors.colorText
  },
  email: {
    fontSize: scale(12),
    fontWeight: "400",
    color: colors.text
  }
})

export default ContractHistory;