/* eslint-disable prettier/prettier */
import { GestureHandlerRootView } from 'react-native-gesture-handler';
import { StyleSheet, TextInput, TouchableOpacity } from 'react-native'
import React, { useState, useCallback, useMemo, useEffect } from 'react'
import {
  Text,
  View,
  SectionsWheelPicker,
  SegmentedControl,
  Button,
  WheelPicker,
  WheelPickerProps,
  Constants,
} from 'react-native-ui-lib';
import { TouchEventType } from 'react-native-gesture-handler/lib/typescript/TouchEventType';


// const DAYS = Array.from({ length: 31 }, (_, i) => i + 1);

const DAYS: (string | number)[] = [];
for (let i = 1; i <= 31; i++) {
  DAYS.push(i);
}
const SectionsPicker = (props: any) => {
  const [numOfSections, setNumOfSections] = useState(1);
  const [disableRTL, setDisableRTL] = useState(false);
  const [selectedDays, setSelectedDays] = useState(1);
  const [selectedMonth, setSelectedMonth] = useState(1);

  useEffect(() => {
    const currentDate = new Date();
    const defaultSelectedDay = currentDate.getDate();
    const defaultSelectedMonth = currentDate.getMonth() + 1;
    setSelectedDays(defaultSelectedDay);
    setSelectedMonth(defaultSelectedMonth);
  }, []);
  const shouldDisableRTL = useMemo(() => {
    return Constants.isRTL && disableRTL;
  }, [disableRTL]);

  const getItems = useCallback((values: (number | string)[]) => {
    return values.map((item: string | number) => ({ label: String(item), value: item }));
  }, []);

  const onDaysChange = useCallback((item: number | string) => {
    setSelectedDays(item as number);
  }, []);

  const onSavePress = useCallback(() => {
    console.log("days", selectedDays);
    props.onSaveSelectedDay(selectedDays);

  }, [selectedDays, props]);

  const sections: WheelPickerProps[] = useMemo(() => {
    return [
      {
        items: getItems(DAYS),
        onChange: onDaysChange,
        initialValue: selectedDays,
        label: Constants.isRTL ? 'ימים' : '',
        style: {
          flex: 1,
          flexDirection: numOfSections !== 1 && Constants.isRTL && !disableRTL ? 'row-reverse' : undefined
        }
      },
    ];
  }, [
    getItems,
    disableRTL,
    selectedDays,
    onDaysChange,
    numOfSections,
    shouldDisableRTL
  ]);

  const sectionsToPresent = useMemo(() => sections.slice(0, numOfSections), [numOfSections, sections]);

  return (

    <GestureHandlerRootView style={styles.container}>
      <View style={{ borderBottomRightRadius: 16, borderBottomLeftRadius: 16 }} >
        <SectionsWheelPicker disableRTL={disableRTL} sections={sectionsToPresent} />
        <TouchableOpacity
          style={styles.btnSave}
          onPress={onSavePress}
        >
          <Text style={{ textAlign: 'center', color: '#FFF', fontSize: 16, lineHeight: 22, fontWeight: '400' }}>Chọn</Text>
        </TouchableOpacity>

      </View>
    </GestureHandlerRootView>
  )
}

export default SectionsPicker

const styles = StyleSheet.create({
  container: {
    width: '100%',
    paddingTop: 37,
  },
  btnSave: {
    backgroundColor: '#A17CBC',
    borderBottomRightRadius: 15,
    borderBottomLeftRadius: 15,
    paddingVertical: 10,
    justifyContent: 'center',
    alignItems: 'center'
  }
})
