import {
  ActivityIndicator,
  FlatList,
  LogBox,
  SafeAreaView,
  StyleSheet,
  Text,
  View,
} from 'react-native';
import Header from '../../component/AppBar';
import InputComponent from '../../component/InputComponent';
import React, { useCallback, useEffect, useState } from 'react';
import { Card, ProgressBar } from 'react-native-ui-lib';
import { colors } from '../../../assets/color/color';
import { Skeleton, VStack } from 'native-base';
import { useFocusEffect } from '@react-navigation/native';
import { getDebitElms } from '../../api/DebitApi';
import { handleShowDate, roundNumber, scale } from '../../utlis/func';
import i18n from "../../service/i18n"
import NullDataComponent from '../../component/NullData';

const PaymentScheduleScreen = (props: any) => {
  LogBox.ignoreAllLogs();
  const [text, onChangeSearch] = useState('');
  const [isLoaded, setIsLoaded] = useState(false);
  const [loading, setLoading] = useState(true);
  const [currentPage, setCurrentPage] = useState(0);
  const [dataPayment, setDataPayment] = useState<any>();

  const { navigation } = props;

  useFocusEffect(
    useCallback(() => {
      handleFetchData(currentPage);
    }, [currentPage]),
  );

  useEffect(() => {
    if (loading) {
      handleFetchData(currentPage);
    }
  }, [loading]);

  const handleEndReach = () => {
    setCurrentPage(prev => ++prev);
  };

  const handleFetchData = async (page: number) => {
    const dataDebitElms = await getDebitElms(page);
    if (page == 0) {
      setDataPayment(dataDebitElms);
      setLoading(false);
    } else {
      setDataPayment([...dataPayment, ...dataDebitElms]);
    }
  };

  const handleRefreshing = () => {
    setLoading(true);
    setCurrentPage(0);
  };

  setTimeout(() => {
    setIsLoaded(true);
  }, 3000);

  const RenderItems = ({ data }: any) => {
    const dateRepaymentPeriod = handleShowDate(
      data.repaymentPeriod,
      false,
      false,
    ).split(' ')[0];
    const createdDate = handleShowDate(data.createdDate);
    return (
      <Card
        key={data.id}
        style={{
          flex: 1,
          borderWidth: 1,
          borderRadius: 8,
          borderColor: '#DDDDDD',
          height: '100%',
          width: '100%',
          justifyContent: 'center',
          alignItems: 'center',
          marginTop: 24,
        }}
        onPress={() => navigation.navigate('RegisServiceContract', { data })}>
        <View
          style={{
            flexDirection: 'row',
            justifyContent: 'center',
            paddingHorizontal: 8,
            paddingVertical: 8,
          }}>
          <VStack
            w="85%"
            maxW="400"
            space={8}
            h="100"
            overflow="hidden"
            rounded="md"
            _dark={{
              borderColor: 'coolGray.500',
            }}
            _light={{
              borderColor: 'coolGray.200',
            }}>
            <Skeleton.Text isLoaded={isLoaded} key={data.id}>
              <Text style={styles.textTitle}>{data.briefId}</Text>
              <Text style={styles.textLabel}>
                {`${i18n.t("loan.loanPayment.monthlyPaymentAmount")}: `}
                <Text style={styles.textValue}>{roundNumber(data.remainingDebt)}</Text>
              </Text>
              <Text style={styles.textLabel}>
                {`${i18n.t("loan.loanPayment.nextTerm")}: `}
                <Text style={styles.textValue}>{dateRepaymentPeriod}</Text>
              </Text>
              <Text style={styles.textLabel}>
                {`${i18n.t('loan.registerContract.dateCreated')}: `}{' '}
                {createdDate}
              </Text>
              {data.percentProgress != 0 ? (
                <ProgressBar
                  style={{
                    width: 312,
                    height: 6,
                    marginTop: 10,
                    marginBottom: 10,
                  }}
                  progress={data.percentProgress}
                  progressColor={colors.primary}
                />
              ) : (
                <ProgressBar
                  style={{
                    width: 312,
                    height: 6,
                    marginTop: 10,
                    marginBottom: 10,
                  }}
                  progress={undefined}
                  progressColor={'#EEEEEE'}
                />
              )}

              <ProgressBar
                style={{
                  width: 312,
                  height: 6,
                  marginTop: 10,
                  marginBottom: 10,
                }}
                progress={data.percentProgress}
                progressColor={colors.primary}
              />
            </Skeleton.Text>
          </VStack>
        </View>
      </Card>
    );
  };

  return (
    <SafeAreaView
      style={{
        backgroundColor: '#fff',
        flex: 1,
      }}>
      <Header
        backgroundColor="#FFF"
        onPress={() => setIsLoaded(false)}
        label={i18n.t('loan.loanPayment.paymentSchedule')}
        {...props}
      />
      <View
        style={{
          flex: 1,
          backgroundColor: 'rgba(255, 255, 255, 1)',
          width: '100%',
          alignItems: 'center',
        }}>
        <View
          style={{ marginVertical: 15, width: '100%', paddingHorizontal: 26 }}>
          <InputComponent
            placeholder={i18n.t('home.search')}
            text={text}
            onChangeText={onChangeSearch}
          />
        </View>
        {dataPayment ? (
          <FlatList
            data={dataPayment}
            renderItem={({ item }) => <RenderItems data={item} />}
            keyExtractor={item => item.id}
            ListFooterComponent={() => loading && <ActivityIndicator />}
            numColumns={1}
            alwaysBounceVertical
            showsVerticalScrollIndicator={false}
            refreshing={loading}
            onRefresh={handleRefreshing}
            onEndReachedThreshold={0.5}
            onEndReached={handleEndReach}
          />
        ) : NullDataComponent()}
      </View>
    </SafeAreaView>
  );
};

const styles = StyleSheet.create({
  textTitle: {
    color: '#62248F',
    fontWeight: '600',
    lineHeight: 22,
    height: 20,
    fontSize: scale(14),
  },
  textLabel: {
    color: '#00204D',
    lineHeight: 20,
    fontWeight: '400',
    fontSize: scale(12),
  },
  textValue: {
    color: '#00204D',
    fontWeight: '600',
    fontSize: scale(12),
    lineHeight: 20,
  },
});

export default PaymentScheduleScreen;
