import AsyncStorage from '@react-native-async-storage/async-storage';
import {BASE_URL_ELMS, TOKEN, pageSize} from '../utlis/contanst';

export const getContractListUserElms = async (page: number) => {
  const token = await AsyncStorage.getItem(TOKEN);
  return fetch(
    BASE_URL_ELMS + `/api/elsm-users?page=${page}&size=${pageSize}`,
    {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        Authorization: 'Bearer' + token,  
      },
    },
  )
    .then(res => res.json())
    .catch(e => console.log(e));
};

export const getContractElmsById = async (briefId: any) => {
  const token = await AsyncStorage.getItem(TOKEN);
  return fetch(BASE_URL_ELMS + `api/loan-briefs/${briefId}`, {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json',
      Authorization: 'Bearer' + token,
    },
  })
    .then(res => res.json())
    .catch(e => console.log(e));
};
