import {Image, Text} from 'react-native';
import {View} from 'react-native-ui-lib';
import {image_null} from '../../assets/images/images';
import {scale} from '../utlis/func';
import { colors } from '../../assets/color/color';

const NullDataComponent = () => {
  return (
    <View style={{flex: 1, justifyContent: 'center', alignItems:'center'}}>
      <View
        style={{
          width: '50%',
          height: '50%',
          alignItems: 'center',
          justifyContent: 'center',
        }}>
        <Image
          style={{
            marginVertical: 50,
            width: 94.67,
            height: 110.9,
            alignItems: 'center',
            resizeMode: 'contain',
          }}
          source={image_null}
        />
        <Text
          style={{
            color: colors.colorText,
            fontWeight: '400',
            fontSize: scale(14),
            lineHeight: 22,
            textAlign: 'center',
          }}>
          Không tìm thấy kết quả phù hợp với tiêu chí của bạn
        </Text>
      </View>
    </View>
  );
};

export default NullDataComponent;
