import React from "react";
import { Dimensions, SafeAreaView } from "react-native";
import Header from "../../component/AppBar";
import { Button, ButtonSize, Image, Text, View } from "react-native-ui-lib";
import { image_ticketSuccess } from "../../../assets/images/images";
import { colors } from "../../../assets/color/color";
import { Pressable } from "native-base";
import i18n from "../../service/i18n"
const SuccessCreateContract = (props: any) => {
  const { navigation } = props;

  const handleDirectToContractDetail = () => {
    navigation.navigate('ContractDetail')
  }

  return (
    <SafeAreaView style={{ flex: 1, backgroundColor: "#fff" }}>
      <Header label={i18n.t("home.titleHome.newProfile")}{...props} />
      <View style={{ flex: 1, rowGap: 10 }} center>
        <Image width={Dimensions.get("screen").width} source={image_ticketSuccess} resizeMode="cover" />
        <Text style={{
          fontSize: 16,
          fontWeight: "600",
          color: "rgba(0, 32, 77, 0.95)"
        }}>{i18n.t("successContract.successfully")}!</Text>
        <Text style={{
          fontSize: 14,
          fontWeight: "400",
          color: "rgba(0, 32, 77, 0.7)"
        }}>{i18n.t("successContract.profileCode")} #456345</Text>
        <Pressable onPress={handleDirectToContractDetail}>
          <Text style={{
            fontSize: 14,
            fontWeight: "500",
            color: "#366AE2"
          }}>{i18n.t("successContract.seeDetails")} </Text>
        </Pressable>
      </View>
      <View style={{ paddingHorizontal: 24, flex: 0.2 }}>
        <Button
          onPress={() => navigation.navigate('MainScreen')}
          style={{
            borderRadius: 8,
            backgroundColor: colors.primary
          }} size={ButtonSize.medium} label={i18n.t("homePage")} />
      </View>
    </SafeAreaView>
  )
}

export default SuccessCreateContract;