import React from 'react';
import {createNativeStackNavigator} from '@react-navigation/native-stack';
import MainScreen from '../screen/MainScreen';
import CreateContractScreen from '../screen/CreateContract/CreateContractScreen';
import GuildeLine from '../screen/CreateContract/GuildeLine';
import MainCreateContract from '../screen/CreateContract/MainCreateContract';
import InfoContract from '../screen/CreateContract/InfoContract';
import MainManagerContract from '../screen/ManageContract/MainManageContract';
import SignContract from '../screen/ManageContract/SignContract';
import NotificationScreen from '../screen/Notification/NotificationScreen';
import EkycScreenFromHostApp from '../screen/EkycFromHostApp/EkycScreen';
import PaymentScheduleScreen from '../screen/Payment/PaymentScheduleScreen';
import HomeSimulation from '../screen/Simulation/HomeSimulation';
import Loan from '../screen/Simulation/Loan';
import ContractComponent from '../screen/Contract/ContractComponent';
import RegisServiceContract from '../screen/Payment/RegisServiceContract';
import SuccessCreateContract from '../screen/CreateContract/SuccessCreateContract';
import ContractDetailComponent from '../screen/Contract/ContractDetail';

const Main = createNativeStackNavigator();
const MainNavigation = (propsx: any) => {
  return (
    <Main.Navigator
      screenOptions={{
        headerShown: false,
      }}>
      <Main.Screen name="MainScreen" component={MainScreen} />
      <Main.Screen name="Contract" component={ContractComponent} />
      <Main.Screen name="ContractDetail" component={ContractDetailComponent} />

      {/* create contract */}
      <Main.Group
        screenOptions={{
          headerShown: false,
        }}>
        <Main.Screen
          name="CreateContractScreen"
          component={CreateContractScreen}
        />
        <Main.Screen name="guildeLine">
          {props => {
            return <GuildeLine {...props} {...propsx} />;
          }}
        </Main.Screen>
        <Main.Screen name="mainCreateContract">
          {props => {
            return <MainCreateContract {...props} />;
          }}
        </Main.Screen>
        <Main.Screen name="createContractSuccess" component={SuccessCreateContract} />
        <Main.Screen name="inFoContract" component={InfoContract} />
        <Main.Screen name="ekyc" component={EkycScreenFromHostApp} />
      </Main.Group>

      {/* manage contract */}
      <Main.Group
        screenOptions={{
          headerShown: false,
        }}>
        <Main.Screen
          name="mainManageContract"
          component={MainManagerContract}
        />
        <Main.Screen name="signContract" component={SignContract} />
      </Main.Group>

      {/* notification */}
      <Main.Group
        screenOptions={{
          headerShown: false,
        }}>
        <Main.Screen name="notification" component={NotificationScreen} />
      </Main.Group>

      <Main.Screen name="PaymentSchedule" component={PaymentScheduleScreen} />
      <Main.Screen name="RegisServiceContract" component={RegisServiceContract} />
      <Main.Screen name="simulation" component={HomeSimulation} />
      <Main.Screen name="loan" component={Loan} />


    </Main.Navigator>
  );
};

export default MainNavigation;
