/* eslint-disable react-native/no-inline-styles */
/* eslint-disable no-trailing-spaces */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable prettier/prettier */

import {
  SafeAreaView,
  Text,
  View,
  TouchableOpacity,
  Dimensions,
  TouchableWithoutFeedback,
  Keyboard,
  StyleSheet,
  Modal,
} from 'react-native';
import { ScrollView } from 'react-native';
import React, { useState, useEffect } from 'react';
import Header from '../../component/AppBar';
import InputComponent from '../../component/InputComponent';
import { colors } from '../../../assets/color/color';
import ModalCustom from './ModalCustom';
import i18n from "../../service/i18n";

const screenWidth = Dimensions.get('screen').width;
const screenHeight = Dimensions.get('screen').height;

const calculateInterest = (
  loanAmount: number,
  interestRate: number,
  numberOfMonths: number,
): number => {
  const interestPercentage = interestRate / 100 / numberOfMonths;
  return loanAmount * interestPercentage;
};

const HomeSimulation = (props: any) => {
  const [loan, setLoan] = useState('');
  const [timeLoan, setTimeLoan] = useState('');
  const [interestRate, setInterestRate] = useState('');
  const [datePayment, setDatePayment] = useState('');
  const [principal, setPrincipal] = useState('');
  const [interest, setInterest] = useState('');
  const [sum, setSum] = useState('');
  const [dataLoan, setDataLoan] = useState<any>([]);

  const [isSectionsPickerVisible, setIsSectionsPickerVisible] = useState(false);
  const [isModalVisible, setIsModalVisible] = useState(false);

  const { navigation } = props;
  const handleBackgroundPress = () => {
    Keyboard.dismiss();
    setIsSectionsPickerVisible(false);
    setIsModalVisible(false);
  };

  const toggleModal = (bool: any) => {
    setIsModalVisible(bool);
  };

  const disableFuc = () => {
    if (
      loan !== '' &&
      timeLoan !== '' &&
      interestRate !== '' &&
      datePayment !== ''
    ) {
      return false;
    }
    return true;
  };

  useEffect(() => {
    let loanAmount = parseFloat(loan);
    const numberOfMonths = parseFloat(timeLoan);
    const interestRateValue = parseFloat(interestRate);

    if (
      !isNaN(loanAmount) &&
      !isNaN(numberOfMonths) &&
      !isNaN(interestRateValue) &&
      numberOfMonths !== 0
    ) {
      const principalValue = loanAmount / numberOfMonths;
      setPrincipal(principalValue.toString());
      let interestValues =
        loanAmount * (interestRateValue / 100 / numberOfMonths);
      setInterest(interestValues.toString());
      let total = principalValue + interestValues;
      setSum(total.toFixed(0).toString());

      let i = 0;
      let dataLoans: any[] = [];
      let dateString;

      if (
        loan !== '' &&
        timeLoan !== '' &&
        interestRate !== '' &&
        datePayment !== ''
      ) {
        const currentDate = new Date();
        while (i < numberOfMonths) {
          const month = currentDate.getMonth() + i;
          const year = currentDate.getFullYear();

          const date = new Date(year, month, Number(datePayment));

          // dateString = `${date.getDate()}/${date.getMonth() + 1
          //   }/${date.getFullYear()}`;
          const interestValue = calculateInterest(
            loanAmount,
            interestRateValue,
            numberOfMonths,
          );

          const remaining = loanAmount - principalValue;
          dataLoans.push({
            id: i,
            debtBriefDate: date,
            totalPrincipal: principalValue,
            totalProfit: interestValue,
            totalOrgItr: total,
            remainingDebt: remaining,
            type: 1,
          });
          total = principalValue + interestValues;
          i++;
          loanAmount = loanAmount - principalValue;
          interestValues =
            loanAmount * (interestRateValue / 100 / numberOfMonths);
          total = principalValue + interestValues;
        }
      }

      setDataLoan(dataLoans);
    } else {
      setPrincipal('');
    }
  }, [loan, timeLoan, interestRate, datePayment]);

  return (
    <TouchableWithoutFeedback onPress={handleBackgroundPress}>
      <SafeAreaView style={{ flex: 1, backgroundColor: '#fff' }}>
        <Header label={i18n.t("simulation.loanSimulation")} {...props} />
        <ScrollView style={{ backgroundColor: '#fff' }} showsVerticalScrollIndicator={false}>

          <View style={styles.titleLoan}>
            <Text
              style={{
                color: '#394960',
                fontWeight: '500',
                fontSize: 14,
                lineHeight: 22,
              }}>
              {i18n.t("simulation.loan")}(VND)
            </Text>
            <Text style={{ color: 'red' }}>*</Text>
          </View>

          <View style={{ width: '100%', paddingHorizontal: 24 }}>
            <InputComponent
              showLeading={false}
              placeholder={i18n.t("simulation.loan")}
              text={loan}
              onChangeText={setLoan}
            />
          </View>

          <View style={styles.title}>
            <Text
              style={{
                color: '#394960',
                fontWeight: '500',
                fontSize: 14,
                lineHeight: 22,
              }}>
              {i18n.t("simulation.borrowedTime")} ({i18n.t("simulation.month")})
            </Text>
            <Text style={{ color: 'red' }}>*</Text>
          </View>
          <View style={{ width: '100%', paddingHorizontal: 24 }}>
            <InputComponent
              showLeading={false}
              placeholder={i18n.t("simulation.borrowedTime")}
              text={timeLoan}
              onChangeText={setTimeLoan}
            />
          </View>

          <View style={styles.title}>
            <Text
              style={{
                color: '#394960',
                fontWeight: '500',
                fontSize: 14,
                lineHeight: 22,
              }}>
              {i18n.t("simulation.interestRate")} (%/{i18n.t("simulation.year")})
            </Text>
            <Text style={{ color: 'red' }}>*</Text>
          </View>

          <View style={{ width: '100%', paddingHorizontal: 24 }}>
            <InputComponent
              showLeading={false}
              placeholder={i18n.t("simulation.interestRate")}
              text={interestRate}
              onChangeText={setInterestRate}
            />
          </View>

          <View style={styles.title}>
            <Text
              style={{
                color: '#394960',
                fontWeight: '500',
                fontSize: 14,
                lineHeight: 22,
              }}>
              {i18n.t("simulation.monthlyPayment")}
            </Text>
            <Text style={{ color: 'red' }}>*</Text>
          </View>

          <TouchableOpacity
            style={{ width: '100%', paddingHorizontal: 24 }}
            onPress={() => toggleModal(true)}>
            <InputComponent
              showLeading={false}
              placeholder={i18n.t("simulation.dates")}
              text={String(datePayment)}
              onChangeText={setDatePayment}
              showDropDown={true}
              readOnly={true}
            />
          </TouchableOpacity>
          <Modal
            transparent={true}
            animationType="fade"
            visible={isModalVisible}
            onRequestClose={() => toggleModal(false)}>
            <ModalCustom
              toggleModal={toggleModal}
              onSaveSelectedDay={setDatePayment}
            />
          </Modal>

          <View
            style={{
              width: screenWidth * 0.88,
              backgroundColor: 'lightgray',
              height: 1.5,
              alignSelf: 'center',
              marginTop: 20,
            }}></View>

          <View style={styles.title}>
            <Text
              style={{
                color: '#394960',
                fontWeight: '500',
                fontSize: 14,
                lineHeight: 22,
              }}>
              {i18n.t("simulation.originalMoney")}
            </Text>
          </View>

          <View style={{ width: '100%', paddingHorizontal: 24 }}>
            <InputComponent
              showLeading={false}
              placeholder={i18n.t("simulation.originalMoney")}
              text={principal}
              editable={false}
              onChangeText={setPrincipal}
            />
          </View>

          <View style={styles.title}>
            <Text
              style={{
                color: '#394960',
                fontWeight: '500',
                fontSize: 14,
                lineHeight: 22,
              }}>
              {i18n.t("simulation.interest")}
            </Text>
          </View>

          <View style={{ width: '100%', paddingHorizontal: 24 }}>
            <InputComponent
              showLeading={false}
              placeholder={i18n.t("simulation.interest")}
              text={interest}
              editable={false}
              onChangeText={setInterest}
            />
          </View>
          <View style={styles.title}>
            <Text
              style={{
                color: '#394960',
                fontWeight: '500',
                fontSize: 14,
                lineHeight: 22,
              }}>
              {i18n.t("simulation.principalInterest")}
            </Text>
          </View>

          <View style={{ width: '100%', paddingHorizontal: 24 }}>
            <InputComponent
              showLeading={false}
              editable={false}
              placeholder={i18n.t("simulation.principalInterest")}
              text={sum}
              onChangeText={setSum}
            />
          </View>

          <View style={{ alignSelf: 'center' }}>
            <TouchableOpacity
              style={{
                alignItems: 'center',
                justifyContent: 'center',
                width: screenWidth * 0.9,
                height: 50,
                borderRadius: 8,
                borderColor: '#A17CBC',
                borderWidth: 1,
                marginTop: 24,
                backgroundColor: disableFuc() ? '#A17CBC' : colors.primary,
              }}
              onPress={() => {
                if (!disableFuc()) {
                  navigation.navigate('loan', {
                    listInstallment: dataLoan,
                    keyTab: 'simulation',
                  });
                }
              }}>
              <Text
                style={{
                  textAlign: 'center',
                  color: '#FFFFFF',
                  fontSize: 14,
                  lineHeight: 22,
                  fontWeight: '500',
                }}>
                {i18n.t("simulation.total")}
              </Text>
            </TouchableOpacity>
          </View>
        </ScrollView>
      </SafeAreaView>
    </TouchableWithoutFeedback>
  );
};

export default HomeSimulation;

const styles = StyleSheet.create({
  inputLight: {
    width: screenWidth * 0.9,
    height: 40,
    borderRadius: 8,
    borderColor: '#003580',
    borderWidth: 1,
    paddingStart: 16,
    marginTop: 8,
  },
  input: {
    width: screenWidth * 0.9,
    height: 40,
    borderRadius: 8,
    borderColor: '#003580',
    borderWidth: 1,
    paddingStart: 16,
    marginTop: 8,
    backgroundColor: '#F9FAFB',
  },
  titleLoan: {
    flexDirection: 'row',
    marginStart: 22,
    marginTop: 33,
    width: screenWidth * 0.9,
    alignItems: 'center',
  },
  title: {
    flexDirection: 'row',
    marginStart: 22,
    marginTop: 16,
    width: screenWidth * 0.9,
    alignItems: 'center',
  },
});
