import React, { useCallback, useEffect, useState } from 'react';
import {
  Dimensions,
  FlatList,
  ImageBackground,
  Modal,
  Platform,
  SafeAreaView,
  ScrollView,
  StyleSheet,
  VirtualizedList,
} from 'react-native';
import {
  Avatar,
  TouchableOpacity,
  Card,
  Carousel,
  Image,
  Text,
  View,
  GridList,
} from 'react-native-ui-lib';
import {
  image_background_noti,
  image_bell,
  image_leftArrow,
  image_noti_loan,
} from '../../assets/images/images';
import LinearGradient from 'react-native-linear-gradient';
import InputComponent from '../component/InputComponent';
import { colors } from '../../assets/color/color';
import FileComponent from '../component/FileComponent';
import { dataSlider, products } from '../utlis/data';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { TOKEN, UserInfo } from '../utlis/contanst';
import { useFocusEffect } from '@react-navigation/native';
import { getDebitElms } from '../api/DebitApi';
import { handleShowDate, roundNumber, scale } from '../utlis/func';
import { getContractElmsById } from '../api/ElmsApi';
import { useTranslation } from 'react-i18next';
import i18n from '../service/i18n';
import languageList from '../service/languageList.json';

const { width, height } = Dimensions.get('screen');
const MainScreen = (props: any) => {
  const { navigation, route } = props;
  const [search, changeSearch] = useState('');
  const [showNoti, setShowNoti] = useState(true);
  const { t } = useTranslation();
  const [ivisible, setIvisible] = useState(false);
  const [changeLang, setChangeLang] = useState(false);
  const changeLanguage = async (language: any) => {
    try {
      await AsyncStorage.setItem('lng', language);
      i18n.changeLanguage(language);
      setIvisible(false);
    } catch (e) {
      console.log(e);
    }
  };

  const [firstDataDebtBrieft, setFirstDataDebtBrieft] = useState<any>();
  const [dataLoanBrief, setDataDataLoanBrief] = useState<any>();
  useFocusEffect(
    useCallback(() => {
      (async () => {
        const dataApi = await getDebitElms(0);
        setFirstDataDebtBrieft(dataApi[0]);
        const dataUserLoanBriefs = await getContractElmsById(
          dataApi[0].briefId,
        );
        setDataDataLoanBrief(dataUserLoanBriefs);
      })();
    }, []),
  );

  useEffect(() => {
    // AsyncStorage.setItem(
    //   TOKEN,
    //   'eyJhbGciOiJSUzI1NiIsInR5cCI6IkpXVCJ9..Ew07tw_IxgA5yE1lNoZEQkpz-JErb2U1_XDTuuut0nOYwUlkGKqsi4DuvvfWMYvmzU9Bo7ixwtW7N5mCbN1gfYmQ1lTig2BquvyTVrqCCGPfoWXufQiRrSw9asUFCxSkfseZOxHKZrubgREmNe66hbu9MtdKGxKuwLRc8hNHoDPn7fsEx8Sdk8RKpDFMaw-insjVYQSO8OHEwar4Rxu0GCBgXAoJ5UBlRNDO4LQAC9BlORzR22T8Inag0wf18iMIclAM5uv-tzB-0YT1CDKyvbuRQoq7MZiasN-eACjYDMmfrRNrx4tcPzgWsXqu0JJqRuiFdR8nlVGVwJGgHEAySg',
    // );
    const loadLanguage = async () => {
      try {
        const userInfo = await AsyncStorage.getItem(UserInfo);
        if (userInfo !== null) {
          i18n.changeLanguage(JSON.parse(userInfo).langKey);
        } else {
          i18n.changeLanguage('vi');
        }
      } catch (e) {
        console.log(e);
      }
    };

    loadLanguage();
  }, []);

  const renderItem = (item: any) => {
    return (
      <TouchableOpacity
        onPress={() => item.direct && navigation.navigate(item.direct)}
        style={{
          alignItems: 'center',
          width: (width - 24 * 2) / 4,
          columnGap: 32,
        }}
        key={item.id}>
        <Image style={styles.image} source={item.pathImage} />
        <Text style={styles.text}>{i18n.t(item.title)}</Text>
      </TouchableOpacity>
    );
  };

  let formatDate: string;
  if (firstDataDebtBrieft) {
    formatDate = handleShowDate(
      firstDataDebtBrieft.repaymentPeriod,
      false,
      true,
    );
  }

  return (
    <View style={{ flex: 1 }}>
      <Carousel
        loop
        autoplay
        autoplayInterval={10 * 1000}
        containerStyle={{
          zIndex: -1,
          height: height * 0.3,
          position: 'absolute',
        }}
        style={{ zIndex: 10 }}>
        {dataSlider.map(item => {
          return (
            <Image
              style={{ height: '100%', width: '100%' }}
              key={item.id}
              source={item.image}
            />
          );
        })}
      </Carousel>
      <SafeAreaView
        style={{
          zIndex: 2,
          position: 'absolute',
          flexDirection: 'row',
          alignItems: 'center',
          justifyContent: 'space-between',
          width: '100%',
          marginVertical: 30,
        }}>
        <TouchableOpacity style={{ marginLeft: 15 }} onPress={() => navigation.goBack()}>
          <Image source={image_leftArrow} style={{ width: 22, height: 22 }} />
        </TouchableOpacity>
        <Avatar
          onPress={() => navigation.navigate('notification')}
          size={40}
          containerStyle={{ marginRight: 15 }}
          source={image_bell}
        />
      </SafeAreaView>
      <SafeAreaView style={styles.container}>
        <View
          style={{
            top: '-3%',
            zIndex: 1,
            paddingHorizontal: '6%',
            position: 'absolute',
            width: '100%',
          }}>
          <InputComponent
            onChangeText={changeSearch}
            text={search}
            placeholder={i18n.t('home.search')}
          />
        </View>
        <LinearGradient
          style={{
            borderTopRightRadius: 20,
            borderTopLeftRadius: 20,
            height: height
          }}
          colors={['#f2edf5', '#fff']}>
          <GridList
            data={products}
            numColumns={1}
            nestedScrollEnabled={true}
            containerWidth={80}
            contentContainerStyle={{
              paddingHorizontal: 24,
              paddingVertical: 50,
            }}
            showsHorizontalScrollIndicator={false}
            horizontal
            renderItem={({ item }) => renderItem(item)}
          />
          <GridList
            data={[{}]}
            containerWidth={width}
            nestedScrollEnabled={true}
            numColumns={1}
            contentContainerStyle={{
              height: '100%',
            }}
            renderItem={({ item }) => (
              <View style={{ width: width, height: "88%" }}>
                <ScrollView showsVerticalScrollIndicator={false}>
                  {/* <FileComponent {...props} /> */}
                  {showNoti && (
                    <ImageBackground
                      source={image_background_noti}
                      style={{ width: '100%' }}>
                      <Card
                        marginR-10
                        marginL-10
                        onPress={() => navigation.navigate('PaymentSchedule')}
                        enableShadow={Platform.OS === 'ios' ? true : false}
                        style={{
                          backgroundColor: 'rgba(0, 32, 77, 0.01)',
                          borderStyle: 'solid',
                          borderWidth: 0.5,
                          borderColor: '#FEBD00',
                          borderRadius: 10,
                          alignItems: 'flex-start',
                          justifyContent: 'center',
                          paddingHorizontal: 15,
                          minHeight: 100
                        }}>
                        <TouchableOpacity
                          style={{ width: '100%' }}
                          onPress={() => setShowNoti(false)}>
                          <Text style={{ textAlign: 'right', marginTop: 8 }}>
                            X
                          </Text>
                        </TouchableOpacity>
                        <View
                          style={{
                            flexDirection: 'row',
                            columnGap: 12,
                            alignItems: 'center',
                          }}>
                          <View style={{ height: 46 }}>
                            <Card.Image
                              source={image_noti_loan}
                              style={{ height: 46, width: 46 }}
                            />
                          </View>
                          <View style={{ rowGap: 1 }}>
                            <Card.Section
                              contentStyle={{}}
                              content={[
                                {
                                  text: t('home.notiHome.noticeLoan'),
                                  color: colors.colorText,
                                  style: {
                                    fontSize: scale(16),
                                    fontWeight: "600",
                                    fontFamily: "Inter"
                                  }
                                },
                              ]}
                            />
                            <Card.Section
                              content={[
                                {
                                  text: `${t('home.bodyHome.agency')}: ${dataLoanBrief ? dataLoanBrief.agency : ''
                                    }`,
                                  style: {
                                    fontSize: scale(14),
                                    fontWeight: "400",
                                    fontFamily: "Inter"
                                  },
                                  color: colors.text,
                                },
                              ]}
                            />
                            <Card.Section
                              content={[
                                {
                                  text: `${t('home.bodyHome.datePayment')}: ${firstDataDebtBrieft ? formatDate : ''
                                    } ~ ${firstDataDebtBrieft
                                      ? roundNumber(firstDataDebtBrieft.debtMoneyPaid)
                                      : ''
                                    }`,
                                  text85: true,
                                  grey10: true,
                                  color: colors.text,
                                },
                              ]}
                            />
                          </View>
                        </View >
                        <TouchableOpacity
                          onPress={() =>
                            navigation.navigate('RegisServiceContract', {
                              data: firstDataDebtBrieft,
                            })
                          }
                          style={{ width: '100%' }}>
                          <Text
                            style={{
                              textAlign: 'right',
                              fontSize: scale(11),
                              fontWeight: '600',
                              color: colors.primary,
                              fontStyle: 'italic',
                              paddingVertical: 6,
                            }}>
                            {t('home.notiHome.detail') + '>>>'}
                          </Text>
                        </TouchableOpacity>
                      </Card >
                    </ImageBackground >
                  )}
                </ScrollView >

              </View >
            )}
          />
        </LinearGradient >
      </SafeAreaView >
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    borderTopRightRadius: 20,
    borderTopLeftRadius: 20,
    top: height * 0.25,
  },
  image: {
    width: 50,
    height: 50,
  },
  text: {
    color: colors.colorText,
    fontSize: scale(14),
    fontWeight: '400',
    textAlign: 'center',
  },
});

export default MainScreen;
