import React, { useState } from "react";
import { StyleSheet } from "react-native";
import { Text, TouchableOpacity, View } from "react-native-ui-lib";
import { colors } from "../../../assets/color/color";
import { StatusContract } from "../../utlis/enum";
import i18n from "../../service/i18n"
const ManageItemComponent = (props: any) => {
  const { navigation, ItemManage } = props;
  const handleSignContract = () => {
    navigation.navigate('signContract', { title: i18n.t(ItemManage.item.title) })
  }

  const handleButtonSign = () => {
    switch (ItemManage.item.status) {
      case StatusContract.cancel:
        return i18n.t("contractManager.cancel")
      case StatusContract.success:
        return i18n.t('contractManager.complete')
      case StatusContract.processing:
        return i18n.t('contractManager.signed')
      case StatusContract.reject:
        return i18n.t('contractManager.reject')
      case StatusContract.expried:
        return i18n.t('contractManager.expired')
      default: break;
    }
  }

  const handleBtnColor = () => {
    switch (ItemManage.item.status) {
      case StatusContract.cancel:
        return {
          color: "#E14337",
          backgroundColor: "#FCEEED"
        }
      case StatusContract.success:
        return {
          color: "#39AC6D",
          backgroundColor: "#E8F7EF"
        }
      case StatusContract.processing:
        return {
          backgroundColor: colors.primary,
          color: "#fff"
        }
      case StatusContract.reject:
        return {
          color: "#E14337",
          backgroundColor: "#FCEEED"
        }
      case StatusContract.expried:
        return {
          color: "#E14337",
          backgroundColor: "#FCEEED"
        }
      default: return {};
    }
  }


  return (
    <View style={{
      padding: 16,
      borderStyle: "solid",
      borderWidth: 1,
      borderColor: "rgba(0, 53, 128, 0.15)",
      alignItems: "center",
      flexDirection: "row",
      justifyContent: "space-between",
      borderRadius: 8,
    }}>
      <View style={{ rowGap: 8 }}>
        <Text style={styles.title}>{i18n.t(ItemManage.item.title)}</Text>
        <Text style={styles.subTitle}>{i18n.t("loan.registerContract.creator")}: {ItemManage.item.createBy}</Text>
        <Text style={styles.subTitle}>{i18n.t("loan.registerContract.receivedDate")}: {ItemManage.item.time}</Text>
      </View>
      <TouchableOpacity
        onPress={handleSignContract}
        style={[{
          backgroundColor: handleBtnColor().backgroundColor,
          borderRadius: 4,
          paddingHorizontal: 20,
          paddingVertical: 5,
          minWidth: 100
        },]}
      >
        <Text style={[{ ...styles.subTitle, color: handleBtnColor().color, textAlign: "center" }]}>
          {handleButtonSign()}
        </Text>
      </TouchableOpacity>
    </View>
  )
}

const styles = StyleSheet.create({
  title: {
    color: "rgba(0, 32, 77, 0.90)",
    fontSize: 14,
    fontWeight: "400"
  },
  subTitle: {
    color: "rgba(0, 32, 77, 0.70)",
    fontSize: 12,
    fontWeight: "400",

  }
})

export default ManageItemComponent;