import React, { useState } from "react";
import { Colors, Image, Picker, PickerModes, Text, TextField, TouchableOpacity, View } from "react-native-ui-lib";
import { image_calender, image_dropdown } from "../../assets/images/images";
import InputComponent from "./InputComponent";
import { GestureHandlerRootView } from "react-native-gesture-handler";
import CalendarScreen from "./DatePickerComponent";
import { Platform } from "react-native";
import i18n from "../service/i18n"
interface IPicker {
  data: any,
  mode?: PickerModes,
  placeholder: string,
  value?: any;
  onChangeValue?: any
}

const PickerComponent = (props: IPicker) => {
  const { mode = PickerModes.SINGLE, placeholder } = props
  const [value, setValue] = useState(props.value);
  const [showCalender, setShowCalender] = useState(false);

  return (
    <GestureHandlerRootView>
      {!(props.placeholder === i18n.t("chechStatusMyContract.titleData.dateCreated")) && (
        <Picker
          containerStyle={{
            borderWidth: 1,
            borderStyle: "solid",
            borderRadius: 8,
            borderColor: "rgba(0, 32, 77, 0.35)",
            paddingBottom: Platform.OS === "ios" ? 10 : 0,
            backgroundColor: "#fff",
            paddingHorizontal: 10,
            minWidth: 130,
          }}
          useDialog
          showSearch
          searchPlaceholder={i18n.t("home.search")}
          searchStyle={{ color: Colors.blue30, placeholderTextColor: Colors.grey50, }}
          placeholderTextColor='rgba(0, 32, 77, 0.55)'
          style={{ padding: 0, fontSize: 12, fontWeight: "400" }}
          placeholder={placeholder}
          value={value}
          onChange={(value) => {
            setValue(value)
            props.onChangeValue(value)
          }}
          floatingPlaceholder={props.value}
          mode={mode}
          selectionLimit={3}
          trailingAccessory={<Image source={image_dropdown} style={{ width: 16, height: 16 }} />}
        >
          {!(props.placeholder === i18n.t("chechStatusMyContract.titleData.dateCreated")) && (
            props.data.map((option: any) => (
              <Picker.Item key={option.id} value={option.value} label={i18n.t(option.label)} />
            ))
          )}
        </Picker>
      )}


      {(props.placeholder === i18n.t("chechStatusMyContract.titleData.dateCreated")) && (
        <>
          <TouchableOpacity onPress={() => setShowCalender(true)}>
            <TextField
              containerStyle={{
                borderWidth: 1,
                borderStyle: "solid",
                borderRadius: 8,
                borderColor: "rgba(0, 32, 77, 0.35)",
                paddingBottom: Platform.OS === "ios" ? 10 : 0,
                backgroundColor: "#fff",
                paddingHorizontal: 10,
                minWidth: 130,
              }}
              editable={false}
              color='#000'
              value={i18n.t("choose")}
              placeholder={i18n.t("chechStatusMyContract.titleData.dateCreated")}
              floatingPlaceholder
              trailingAccessory={<Image source={image_calender} width={16} height={16} />}
            />
          </TouchableOpacity>
          {showCalender && <CalendarScreen showCalender={showCalender} setShowCalender={setShowCalender} />}
        </>
      )}


    </GestureHandlerRootView>
  )
}

export default PickerComponent;