import { ScrollView, StyleSheet, TouchableOpacity } from "react-native";
import { Card, ExpandableSection, GridList, Image, Text, View } from "react-native-ui-lib";
import { image_avatar, image_dropdown, image_rightArrow12px } from "../../../assets/images/images";
import { useState } from "react";
import { handleShowDate, scale } from "../../utlis/func";
import i18n from "../../service/i18n";

const ProfileInformation = (props: any) => {
  const [expand, setExpand] = useState(true);
  return (
    <View
      style={{
        flex: 1,
        flexDirection: 'column',
        width: '100%',
        height: '100%',
      }}>
      <View
        style={{
          flex: 3,
          backgroundColor: '#FFF',
          width: '100%',
          height: '100%',
          marginTop: '2%',
          paddingHorizontal: 17.5,
        }}>
        <ScrollView>
          <ExpandableSection
            top={false}
            expanded={expand}
            sectionHeader={
              <View
                style={{
                  flexDirection: 'row',
                  alignItems: 'center',
                  justifyContent: 'space-between',
                  marginTop: 20,
                }}>
                <Text style={styles.textTitle}>{i18n.t("loan.registerContract.generalInformation")}</Text>
                <TouchableOpacity onPress={() => setExpand(!expand)}>
                  {expand ? (
                    <Image
                      style={{ width: 18, height: 12 }}
                      source={image_dropdown}
                    />
                  ) : (
                    <Image
                      style={{ width: 18, height: 12 }}
                      source={image_rightArrow12px}
                    />
                  )}
                </TouchableOpacity>
              </View>
            }>
            <Card key={0} style={{ marginBottom: 10 }}>
              <View
                style={{
                  flexDirection: 'column',
                  alignItems: 'flex-start',
                  justifyContent: 'center',
                }}>
                <View style={styles.rowValue}>
                  <Card.Section
                    content={[
                      {
                        text: (`${i18n.t("loan.registerContract.profileName")} :`),
                        text12: true,
                        style: {
                          justifyContent: 'flex-start',
                          fontWeight: '600',
                          lineHeight: 20,
                          color: '#00204D',
                        },
                      },
                    ]}
                    style={{ paddingLeft: 10, flex: 1 }}
                  />
                  <Card.Section
                    content={[
                      {
                        text: props.briefName,
                        text12: true,
                        style: {
                          fontWeight: '400',
                          lineHeight: 16,
                          color: 'rgba(0, 32, 77, 0.90)',
                          fontSize: scale(12)
                        },
                      },
                    ]}
                    style={{ paddingLeft: 10, flex: 2 }}
                  />
                </View>
                <View style={styles.rowValue}>
                  <Card.Section
                    content={[
                      {
                        text: (`${i18n.t("loan.registerContract.profileCode")} :`),
                        text12: true,
                        style: {
                          fontWeight: '600',
                          lineHeight: 20,
                          color: '#00204D',
                        },
                      },
                    ]}
                    style={{ paddingLeft: 10, flex: 1 }}
                  />
                  <Card.Section
                    content={[
                      {
                        text: props.briefCode,
                        text12: true,
                        style: {
                          fontWeight: '400',
                          lineHeight: 16,
                          color: 'rgba(0, 32, 77, 0.90)',
                          fontSize: scale(12)
                        },
                      },
                    ]}
                    style={{ paddingLeft: 10, flex: 2 }}
                  />
                </View>
                <View style={styles.rowValue}>
                  <Card.Section
                    content={[
                      {
                        text: (`${i18n.t("home.bodyHome.agency")} :`),
                        text12: true,
                        style: {
                          fontWeight: '600',
                          lineHeight: 20,
                          color: '#00204D',
                        },
                      },
                    ]}
                    style={{ paddingLeft: 10, flex: 1 }}
                  />
                  <Card.Section
                    content={[
                      {
                        text: props.agency,
                        text12: true,
                        style: {
                          fontWeight: '400',
                          lineHeight: 16,
                          color: 'rgba(0, 32, 77, 0.90)',
                          fontSize: scale(12)
                        },
                      },
                    ]}
                    style={{ paddingLeft: 10, flex: 2 }}
                  />
                </View>
                <View
                  style={{
                    flexDirection: 'row',
                    columnGap: 50,
                    marginTop: 10,
                    marginBottom: 10,
                    alignItems: 'center',
                    justifyContent: "center"
                  }}>
                  <Card.Section
                    content={[
                      {
                        text: 'Người tạo :',
                        text12: true,
                        style: {
                          fontWeight: '600',
                          lineHeight: 20,
                          color: '#00204D',
                        },
                      },
                    ]}
                    style={{ paddingLeft: 10, flex: 1 }}
                  />
                  <View style={{ flexDirection: 'row', flex: 2, alignItems: "center", justifyContent: "center" }}>
                    <Card.Section
                      style={{ height: 30, width: 30, borderRadius: 15 }}
                      imageSource={{ uri: props.avatarCustomer }}
                      imageStyle={{ height: 30, width: 30, borderRadius: 15 }}
                    />

                    <View style={{ rowGap: 5 }}>
                      <Card.Section
                        content={[
                          {
                            text: props.fullName,
                            text12: true,
                            style: {
                              fontWeight: '400',
                              lineHeight: 16,
                              color: 'rgba(0, 32, 77, 0.90)',
                            },
                          },
                        ]}
                        style={{ paddingLeft: 10 }}
                      />
                      <Card.Section
                        content={[
                          {
                            text: props.email,
                            text12: true,
                            style: {
                              fontWeight: '400',
                              lineHeight: 16,
                              color: 'rgba(0, 32, 77, 0.70)',
                              fontSize: scale(12)
                            },
                          },
                        ]}
                        style={{ paddingLeft: 10 }}
                      />
                    </View>
                  </View>
                </View>
                <View style={styles.rowValue}>
                  <Card.Section
                    content={[
                      {
                        text: 'Ngày tạo :',
                        text12: true,
                        style: {
                          fontWeight: '600',
                          lineHeight: 20,
                          color: '#00204D',
                        },
                      },
                    ]}
                    style={{ paddingLeft: 10, flex: 1 }}
                  />
                  <Card.Section
                    content={[
                      {
                        text: handleShowDate(props.creationDate),
                        text12: true,
                        style: {
                          fontWeight: '400',
                          lineHeight: 16,
                          color: 'rgba(0, 32, 77, 0.90)',
                          fontSize: scale(12)
                        },
                      },
                    ]}
                    style={{ paddingLeft: 10, flex: 2 }}
                  />
                </View>
              </View>
            </Card>
          </ExpandableSection>
        </ScrollView>

      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  generalInfo: {
    flex: 1,
    paddingHorizontal: 8,
  },
  rowValue: {
    flexDirection: 'row',
    columnGap: 50,
    marginTop: 10,
    marginBottom: 10,
    alignItems: 'center',
  },
  textTitle: {
    fontWeight: '600',
    fontSize: scale(16),
    lineHeight: 24,
    color: '#00204D',
  },
});

export default ProfileInformation;