import {useFocusEffect} from '@react-navigation/native';
import {useCallback, useState} from 'react';
import {
    Image,
  SafeAreaView,
  StyleSheet,
  Text,
  TextInput,
  TouchableOpacity,
  View,
} from 'react-native';
import {getLoanBriefDetails, getLoanBriefs} from '../api/DebitApi';
import i18n from '../service/i18n';
import {image_dropdown, image_rightArrow12px} from '../../assets/images/images';
import {scale} from '../utlis/func';
import {colors} from '../../assets/color/color';
import { Card, ExpandableSection } from 'react-native-ui-lib';
const ContractInfo = (dataBrief: any) => {
  const [expand, setExpand] = useState(true);
  const [dataUserLoanBrief, setDataUserLoanBrief] = useState<any>();
  const [dataLoanBrief, setDataLoanBrief] = useState<any>();
  useFocusEffect(
    useCallback(() => {
      (async () => {
        const dataUserLoan = await getLoanBriefs(0,1);
        setDataUserLoanBrief(dataUserLoan[0]);
        const dataApi = await getLoanBriefDetails(dataBrief.dataBriefId);
        setDataLoanBrief(dataApi);
      })();
    }, []),
  );

  return (
    <SafeAreaView
      style={{
        flex: 3,
        backgroundColor: '#FFF',
        width: '100%',
        height: '100%',
        marginTop: '2%',
      }}>
      <ExpandableSection
        top={false}
        expanded={expand}
        sectionHeader={
          <View
            style={{
              flexDirection: 'row',
              alignItems: 'center',
              justifyContent: 'space-between',
              marginTop: 20,
            }}>
            <Text style={styles.textTitle}>
              {i18n.t('loan.registerContract.contractInformation')}
            </Text>
            <TouchableOpacity onPress={() => setExpand(!expand)}>
              {expand ? (
                <Image
                  style={{width: 18, height: 12}}
                  source={image_dropdown}
                />
              ) : (
                <Image
                  style={{width: 18, height: 12}}
                  source={image_rightArrow12px}
                />
              )}
            </TouchableOpacity>
          </View>
        }>
        <Card key={0} style={{marginTop:15}}>
          <View
            style={{
              flexDirection: 'column',
              alignItems: 'flex-start',
              justifyContent: 'center',
              backgroundColor: '#F9FAFB',
              borderRadius:8,
            }}>
            <View style={styles.rowValue}>
              <Card.Section
                content={[
                  {
                    text: `${i18n.t('loan.registerContract.customer')} :`,
                    text12: true,
                    style: {
                      justifyContent: 'flex-start',
                      fontWeight: '600',
                      lineHeight: 22,
                      color: colors.colorText,
                    },
                  },
                ]}
                style={{paddingLeft: 10, flex: 1}}
              />
              <Card.Section
                content={[
                  {
                    text: dataUserLoanBrief
                      ? dataUserLoanBrief.customerName
                      : '',
                    style: {
                      fontWeight: '600',
                      fontSize: scale(14),
                      lineHeight: 22,
                      color: colors.colorText,
                    },
                  },
                ]}
                style={{paddingLeft: 10, flex: 2}}
              />
            </View>
            <View style={styles.rowValue}>
              <Card.Section
                content={[
                  {
                    text: `${i18n.t('loan.registerContract.phoneNumber')} :`,
                    style: {
                        fontWeight: '400',
                        fontSize: scale(14),
                        lineHeight: 22,
                        color: colors.colorText,
                      },
                  },
                ]}
                style={{paddingLeft: 10, flex: 1}}
              />
              <Card.Section
                content={[
                  {
                    text: dataUserLoanBrief ? dataUserLoanBrief.phone : '',
                    style: {
                      fontWeight: '400',
                      fontSize: scale(14),
                      lineHeight: 22,
                      color: colors.colorText,
                    },
                  },
                ]}
                style={{paddingLeft: 10, flex: 2}}
              />
            </View>
            <View style={styles.rowValue}>
              <Card.Section
                content={[
                  {
                    text: `${i18n.t('loan.registerContract.address')} :`,
                    style: {
                        fontWeight: '400',
                        lineHeight: 22,
                        fontSize: scale(14),
                        color: colors.colorText,
                      },
                  },
                ]}
                style={{paddingLeft: 10, flex: 1}}
              />
              <Card.Section
                content={[
                  {
                    text: dataUserLoanBrief ? dataUserLoanBrief.address : '',
                    style: {
                      fontWeight: '400',
                      fontSize: scale(14),
                      lineHeight: 22,
                      color: colors.colorText,
                    },
                  },
                ]}
                style={{paddingLeft: 10, flex: 2}}
              />
            </View>
          </View>
          <View
            style={{
              flexDirection: 'column',
              alignItems: 'flex-start',
              justifyContent: 'center',
              backgroundColor: '#F9FAFB',
              borderRadius:8,
              marginTop:15
            }}>
            <View style={styles.rowValue}>
              <Card.Section
                content={[
                  {
                    text: `${i18n.t('loan.registerContract.typeVehicle')} :`,
                    style: {
                        fontWeight: '400',
                        lineHeight: 22,
                        fontSize: scale(14),
                        color: colors.colorText,
                      },
                  },
                ]}
                style={{paddingLeft: 10, flex: 1, justifyContent: 'center'}}
              />
              <TextInput
                style={styles.input}
                multiline
                value={dataLoanBrief ? dataLoanBrief.productType : ''}
              />
            </View>
            <View style={styles.rowValue}>
              <Card.Section
                content={[
                  {
                    text: `${i18n.t('loan.registerContract.vehicleInfo')} :`,
                    style: {
                        fontWeight: '400',
                        lineHeight: 22,
                        fontSize: scale(14),
                        color: colors.colorText,
                      },
                  },
                ]}
                style={{paddingLeft: 10, flex: 1}}
              />
              <TextInput
                style={{
                  width: scale(208),
                  height: scale(40),
                  borderWidth: 1,
                  borderRadius: 8,
                  borderColor: '#CCCCCC',
                  padding: 10,
                  fontWeight: '500',
                  fontSize: scale(14),
                  lineHeight: 22,
                  color: colors.colorText,
                }}
                multiline
                value={dataLoanBrief ? dataLoanBrief.productValue : ''}
              />
            </View>
            <View style={styles.rowValue}>
              <Card.Section
                content={[
                  {
                    text: `${i18n.t('loan.registerContract.colorVehicle')} :`,
                    style: {
                      fontWeight: '400',
                      lineHeight: 22,
                      fontSize: scale(14),
                      color: colors.colorText,
                    },
                  },
                ]}
                style={{paddingLeft: 10, flex: 1}}
              />
              <TextInput
                style={{
                  width: scale(208),
                  height: scale(40),
                  borderWidth: 1,
                  borderRadius: 8,
                  borderColor: '#CCCCCC',
                  padding: 10,
                  fontWeight: '500',
                  fontSize: scale(14),
                  lineHeight: 22,
                  color: colors.colorText,
                }}
                multiline
                value={dataLoanBrief ? dataLoanBrief.productValue : ''}
              />
            </View>
          </View>
        </Card>
      </ExpandableSection>
    </SafeAreaView>
  );
};

const styles = StyleSheet.create({
  generalInfo: {
    flex: 1,
    paddingHorizontal: 8,
  },
  rowValue: {
    flexDirection: 'row',
    columnGap: 50,
    marginTop: 10,
    marginBottom: 10,
    alignItems: 'center',
  },
  textTitle: {
    fontWeight: '600',
    fontSize: 16,
    lineHeight: 24,
    color: '#00204D',
  },
  input: {
    width: scale(208),
    height: scale(56),
    borderWidth: 1,
    borderRadius: 8,
    borderColor: '#CCCCCC',
    padding: 10,
    fontWeight: '500',
    fontSize: scale(14),
    lineHeight: 22,
    color: '#00204D',
  },
});

export default ContractInfo;
