import React, { useCallback, useState } from "react";
import { SafeAreaView, StyleSheet } from "react-native";
import Header from "../../component/AppBar";
import { Button, ButtonSize, Carousel, Text, View } from "react-native-ui-lib";
import { colors } from "../../../assets/color/color";
import InputComponent from "../../component/InputComponent";
import PickerComponent from "../../component/PickerComponent";
import ListBankComponent from "../../component/ListBank";
import { options } from "../../utlis/data";
import i18n from "../../service/i18n";

const MainCreateContract = (props: any) => {
  const { navigation, route, title } = props;
  console.log(props);

  const [valuePicker, setValuePicker] = useState('')

  const handleSubmit = useCallback(() => {
    // props.setX(30);
  }, [])

  return (
    <SafeAreaView style={{ flex: 1, backgroundColor: "#fff" }}>
      <Header label={i18n.t("home.titleHome.newProfile")} {...props} />
      <View style={{ flex: 1, alignItems: "flex-start", justifyContent: "center", rowGap: 8, paddingHorizontal: 20 }}>
        <Text center style={styles.textLabel}>
          {i18n.t("createContract.loanProducts")} 
          <Text style={{ ...styles.textLabel, ...styles.colorRed }}>*</Text>
        </Text>
        <InputComponent text={title} editable={false} showLeading={false} />

        <Text center style={{ ...styles.textLabel }}>
            {i18n.t("loan.registerContract.profileName")}
          <Text style={{ ...styles.textLabel, ...styles.colorRed }}>*</Text>
        </Text>
        <InputComponent placeholder={i18n.t("createContract.enterBriefName")}  showLeading={false} />

        <Text center style={styles.textLabel}>
            {i18n.t("home.bodyHome.agency")}
          <Text style={{ ...styles.textLabel, ...styles.colorRed }}>*</Text>
        </Text>
        <View style={{ width: "100%" }}>
          <PickerComponent
            data={options}
            placeholder={i18n.t("createContract.selectDealer")} 
            value={options[0].value}
            onChangeValue={setValuePicker} />
        </View>
      </View>
      <View style={{ flex: 1, alignItems: "flex-start", justifyContent: "center", rowGap: 8 }}>
        <View style={{ paddingHorizontal: 20 }}>
          <Text style={styles.textLabel}>
            {i18n.t("createContract.selectBank")} 
            <Text style={{ ...styles.textLabel, ...styles.colorRed }}>*</Text>
          </Text>
        </View>
        <Carousel
          containerStyle={{
            height: "70%",
            backgroundColor: "#fff"
          }}
          showsHorizontalScrollIndicator={false}
          pagingEnabled
          pageControlPosition={Carousel.pageControlPositions.UNDER}
          allowAccessibleLayout
        >
          {[{}, {}, {}, {}, {}, {}, {}, {}].map((item) => {
            return (
              <ListBankComponent />
            )
          })}
        </Carousel>
      </View>
      <View style={{ flex: 0.5, paddingHorizontal: 24 }}>
        <Button backgroundColor={colors.primary} labelStyle={{
          fontSize: 14,
          color: "#fff",
          fontWeight: "500"
        }} onPress={handleSubmit} label={i18n.t("home.continue")}  size={ButtonSize.large} borderRadius={8} />
      </View>
    </SafeAreaView>
  )
}

const styles = StyleSheet.create({
  textLabel: {
    fontSize: 14,
    fontWeight: "500",
    color: colors.colorTitie,
    marginTop: 12
  },
  colorRed: {
    color: "#E14337"
  }
})

export default MainCreateContract;