import {StyleSheet, View, Dimensions, TouchableOpacity} from 'react-native';
import React from 'react';
import SectionsPicker from './SectionsPicker';

const screenWidth = Dimensions.get('screen').width;
const screenHeight = Dimensions.get('screen').height;

const ModalCustom = (props: any) => {
  const onPressItem = (option: any) => {
    console.log("option", option);
    
    props.toggleModal(false);
    props.onSaveSelectedDay(option);
  };

  return (
    <TouchableOpacity
      onPress={() => props.toggleModal(false)}
      style={{flex: 1, justifyContent: 'center', alignItems: 'center', backgroundColor: 'rgb(0, 0, 0, 0,5)' }}>
      <View
        style={{
          borderRadius: 16,
          width: screenWidth - 27,
          justifyContent: 'center',
          alignItems: 'center',
          borderWidth: 1,
          borderColor: 'lightgray',
          paddingTop: 10,
          height: '37%',
          backgroundColor: '#fff',
          marginTop: 400
        }}>

        <SectionsPicker onSaveSelectedDay={onPressItem} />

      </View>
    </TouchableOpacity>
  );
};

export default ModalCustom;

const styles = StyleSheet.create({});
