import React from "react";
import { Card, GridList, GridView, View } from "react-native-ui-lib";
import { image_tpbank } from "../../assets/images/images";
import { Dimensions } from "react-native";

const dataBank = [
  { id: 1, img: image_tpbank, selected: true },
  { id: 2, img: image_tpbank, selected: false },
  { id: 3, img: image_tpbank, selected: false },
  { id: 4, img: image_tpbank, selected: false },
  { id: 5, img: image_tpbank, selected: false },
  { id: 6, img: image_tpbank, selected: false },
]

const ListBankComponent = () => {

  const renderBankItem = ({ item }: any) => {
    return (
      <Card
        key={item.id}
        height={58}
        width={100}
        flex
        containerStyle={{ paddingRight: 0 }}
        center
        selected={item.selected}
        activeOpacity={1}
        marginT-10
        marginL-12
        marginR-12
        onPress={() => console.log(11)}
      >
        <Card.Image cover source={item.img} height={27} width={60} />
      </Card>
    )
  }

  return (
    <GridList
      showsHorizontalScrollIndicator={false}
      showsVerticalScrollIndicator={false}
      data={dataBank}
      numColumns={3}
      maxItemWidth={Dimensions.get("screen").width / 3}
      renderItem={renderBankItem}
    />
  )
}

export default ListBankComponent;