import AsyncStorage from '@react-native-async-storage/async-storage';
import {BASE_URL_ELMS, TOKEN, pageSize} from '../utlis/contanst';

export const getDebitElms = async (page: number) => {
  const token = await AsyncStorage.getItem(TOKEN);
  return fetch(BASE_URL_ELMS + `api/debt-briefs?page=${page}&size=${pageSize}`, {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json',
      Authorization: 'Bearer' + token,
    },
  })
    .then(res => res.json())
    .catch(e => console.log(e));
};

