import React, { useCallback, useState } from "react";
import { FlatList, ScrollView } from "react-native";
import { Chip, View } from "react-native-ui-lib";
import i18n from "../service/i18n"
const ScrollHoriz = (props: any) => {
  const [listStatus, setListStatus] = useState(props.listStatus)
  const handleChangeSeleced = useCallback((id: number) => {
    const newList = listStatus.map((item: any) => {
      if (item.id === id) {
        props.setChipSelected(item);
        return {
          ...item,
          selected: true
        }
      } else {
        return {
          ...item,
          selected: false
        }
      }
    })
    setListStatus(newList)
  }, [JSON.stringify(listStatus)])
  return (
    <View>
      <FlatList
        horizontal
        data={listStatus}
        showsVerticalScrollIndicator={false}
        showsHorizontalScrollIndicator={false}
        contentContainerStyle={{
          alignItems: "flex-start",
          justifyContent: "flex-start",
          columnGap: 10,
          paddingHorizontal: 24,
          marginBottom: 20
        }}
        renderItem={({ item }) => (
          <>
            <Chip
              key={item.id}
              containerStyle={{
                borderRadius: 6,
                backgroundColor: item.selected ? "#F2E0FE" : "#F9FAFB",
                borderWidth: 0,
              }}

              labelStyle={{
                fontSize: 14,
                textAlign: "center",
                color: item.selected ? "#62248F" : "rgba(0, 32, 77, 0.55)"
              }}
              label={i18n.t(item.name)}
              onPress={() => handleChangeSeleced(item.id)} />
          </>
        )}
      />

    </View>
  )
}

export default ScrollHoriz;