const CreateContractScreen = (props: any) => {
  const [listService, setListService] = useState();
  const [isFreshing, setIsFreshing] = useState(false);
  const setServiceSelected = useMyContractStore((state: any) => state.setServiceSelected);

  useFocusEffect(useCallback(() => {
    (async () => {
      const data = await searchListServices()
      console.log(data,'data')
      setListService(data)
    })()
  }, []))

  const handleSaveServiceSelected = (item: any) => {
    setServiceSelected(item);
    navigation.navigate('mainCreateContract', { title: item.flowName })
  }

  const { navigation } = props;
  const renderItemCreateContract = ({ item }: any) => {

    return (
      <Card
        margin-15
        containerStyle={{ alignItems: "center", justifyContent: "center", minHeight: 80 }}
        backgroundColor="#FBFBFB"
        borderRadius={16}
        onPress={() => handleSaveServiceSelected(item)}
      >
        <View style={{ paddingHorizontal: 18 }}>
          <Text style={{ fontSize: scale(14), fontWeight: "400", color: "rgba(0, 32, 77, 0.95)", textAlign: "center" }}>{item.flowName}</Text>
        </View>
      </Card>
    )
  }
  return (
    <View style={{ backgroundColor: "#fff", flex: 1 }}>
      <SafeAreaView>
        <Header label={i18n.t("createContract.createLoanContract")}{...props} />
        {/* <TabComponent /> */}
        {listService ? (
          <GridList
            refreshing={true}
            contentContainerStyle={{ marginTop: 20, height: "100%" }}
            data={listService}
            renderItem={renderItemCreateContract}
            numColumns={2}
          />
        ) : (
          <GridList
            refreshing={true}
            contentContainerStyle={{ marginTop: 20, height: "100%" }}
            data={[{}, {}, {}, {}]}
            renderItem={() => (
              <Skeleton style={{ paddingHorizontal: 18 }} h="20" />
            )}
            numColumns={2}
          />
        )}
      </SafeAreaView>
    </View>
  )
}

export default CreateContractScreen;