import React, { useCallback, useState } from "react";
import { SafeAreaView, useWindowDimensions } from "react-native";
import Header from "../../component/AppBar";
import { Text, TouchableOpacity, View } from "react-native-ui-lib";
import InputComponent from "../../component/InputComponent";
import { SceneMap, TabBar, TabView } from "react-native-tab-view";
import { handleShowString } from "../../utlis/func";
import { useFocusEffect } from "@react-navigation/native";
import { getLoanById } from "../../api/DebitApi";
import ProfileInformation from "./ProfileInfo";
import ContractHistory from "./ContractHistory";
import i18n from "../../service/i18n"

const ContractDetailComponent = (props: any) => {
  const { navigation, route } = props;
  const [index, setIndex] = useState(0);
  const layout = useWindowDimensions();
  const [dataContractDetail, setDataContractDetail] = useState();
  const [routes] = useState([
    { key: 'first', title: (i18n.t("successContract.procedure")) },
    { key: 'second', title: (i18n.t("loan.registerContract.profileInfo")) },
    { key: 'three', title: (i18n.t("successContract.briefHistory")) },
  ]);
  const renderScene = SceneMap({
    first: () => <View style={{ marginTop: 10, backgroundColor: "#fff", height: "100%" }}><Text>1</Text></View>,
    second: () => ProfileInformation(dataContractDetail),
    three: () => ContractHistory({ id: route?.params?.id })
  });



  const renderTabBar = (props: any) => {
    return (
      <TabBar
        {...props}
        indicatorStyle={{ backgroundColor: '#62248F' }}
        style={{ backgroundColor: '#FFF' }}
        labelStyle={{ color: 'rgba(0, 32, 77, 0.70)', fontSize: 10, fontWeight: "400", fontFamily: "Inter" }}
        activeColor='#62248F'
      />
    );
  };

  useFocusEffect(useCallback(() => {
    (async () => {
      if (route?.params?.id) {
        const data = await getLoanById(route?.params?.id);
        setDataContractDetail(data);
      }
    })()
  }, []))


  return (
    <View style={{ flex: 1, backgroundColor: "#F8FAFE", borderRadius: 17, position: "relative" }}>
      <Header containerStyle={{
        height: "15%",
      }} backgroundColor='#F2E0FE' color={'#62248F'} label={handleShowString(route?.params?.briefName)} {...props} />
      <SafeAreaView
        style={{
          backgroundColor: "rgba(255, 255, 255, 1)",
          position: "absolute",
          top: "13%",
          borderTopLeftRadius: 17,
          borderTopRightRadius: 17,
          height: "88%",
          width: "100%"
        }}>
        {dataContractDetail && (
          <TabView
            lazy={true}
            renderTabBar={renderTabBar}
            navigationState={{ index, routes }}
            renderScene={renderScene}
            onIndexChange={setIndex}
            style={{
              borderTopLeftRadius: 17,
              borderTopRightRadius: 17,
            }}
            sceneContainerStyle={{
              backgroundColor: "#F2F5F8"
            }}
            initialLayout={{ width: layout.width }}
          />
        )}
      </SafeAreaView>
    </View>
  )
}

export default ContractDetailComponent;